import React, { useState, useEffect, Fragment } from 'react';
import {
  Grid,
  Table,
  TableContainer,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  IconButton,
} from '@material-ui/core';
import { useSelector } from 'react-redux';
import { getPatientsList, getSearchPatientList } from 'apis/patientAPI';
import PatientDrawer from '../../../components/patient/drawer/PatientDrawer';
import PatientUpdateDrawer from '../../../components/patient/drawer/PatientUpdateDrawer';
import { GrPowerReset } from 'react-icons/gr';
import ContentContainer from 'components/common/container/ContentContainer';
import TitleHeader from 'components/common/header/TitleHeader';
import StyledButton from 'components/common/button/StyledButton';
import SearchBox from 'components/common/search/SearchBox';
import ResponsivePageHeader from 'components/common/header/ResponsivePageHeader';
import PageTransition from 'components/common/transition/PageTransition';
import ClockSpinner from 'components/common/spinner/ClockSpinner';
import moment from 'moment';

/**
 *         .
 *    .
 * * Sider
 * * Header
 * *   (PatientSearch, Table, ColoredButton)
 * @returns {JSX.Element}
 * @author SI HYUN PARK
 */
const PatientPage = () => {
  //        open/close   
  const [isOpened, setOpened] = useState(false);

  //          open/close   
  const [isUpdateOpened, setUpdateOpened] = useState(false);

  //    
  const [readPatientData, setReadPatientData] = useState({});

  //         ([{1}, {2}.....])
  const [patientData, setPatients] = useState([]);

  // spinner  
  const [isLoading, setLoading] = useState(true);

  //   addDisplay   .           
  const [addDisplay, setDisplay] = useState(false);

  //    
  const loginInfo = useSelector((state) => state.common.loginInfo);

  //    ,   
  useEffect(() => {
    const getPatient = async () => {
      try {
        const { data } = await getPatientsList(loginInfo.hospitalCode);
        setPatients(data.data);
      } catch (error) {
        console.log(error);
        setPatients([]);
        setLoading(false);
      }
    };
    getPatient();
  }, [loginInfo]);

  //  (patientData)   isLoading false   
  useEffect(() => {
    if (patientData.length > 0) {
      setLoading(false);
    }
  }, [patientData]);

// ,  addDisplay  ,       
  useEffect(() => {
    if(addDisplay) {
      setLoading(true);
      const getAddPatient = async () => {
        try {
          const { data } = await getPatientsList(loginInfo.hospitalCode);
          setPatients(data.data);
        } catch (error) {
          console.log(error);
          setPatients([]);
          setLoading(false);
        }
      };
      getAddPatient();
      setDisplay(false);
    }
  }, [addDisplay, loginInfo])

  const resetOnClick = async () => {
      setLoading(true);
      try {
          const { data } = await getPatientsList(loginInfo.hospitalCode);
          setPatients(data.data);
        } catch (error) {
          console.log(error);
          setPatients([]);
          setLoading(false);
        }
  }


  //       
  const setSearchVal = async (inputVal) => {
    
    setLoading(true);
    try {
      const { data } = await getSearchPatientList({
        hospitalCode: loginInfo.hospitalCode,
        patientName: inputVal,
      });
      setPatients(data.data);
    } catch (error) {
      setPatients([]);
      console.log(error.response.data);
      setLoading(false);
     
    }
  };
  //  
  const clockSpinner = () => {
    return (
      <div
        style={{
          height: '100vh',
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
        }}
      >
        <ClockSpinner isLoading={isLoading} />
      </div>
    );
  };

  return (
    <div>
      <header
        style={{
          position: 'sticky',
          top: 0,
          backgroundColor: 'white',
          zIndex: 1,
        }}
      >
        <ResponsivePageHeader />
      </header>
      <main>
        <Grid container>
          <Grid item xs={12}>
            <PageTransition>
              <ContentContainer>
                <TitleHeader>
                  <span> | </span>
                  <span> </span>
                </TitleHeader>
                <br />
                <Grid container justify="center" >
                  <Grid item xs={8} lg={6}>
                    <SearchBox
                      setSearchVal={setSearchVal}
                      placeholder="  ."
                    />
                  </Grid>
                  <Grid item xs={2} lg={2} >
                    <IconButton
                      type="button"
                      size="medium"
                      style={{
                        border: '1px solid rgba(0,0,0,0.12)',
                        marginLeft: '0.5rem',
                        marginRight: '0.5rem',
                        marginTop : '0.5rem'
                      }}
                      onClick={resetOnClick}
                    >
                      <GrPowerReset />
                    </IconButton>
                  </Grid>
                  <Grid
                    item
                    xs={2}
                    lg={4}
                    style={{
                      display: 'flex',
                      alignItems: 'center',
                      justifyContent: 'flex-end',
                    }}
                  >
                    <StyledButton
                      width="100px"
                      bgColor="rgb(30, 51, 71)"
                      color="white"
                      onClick={() => {
                        setOpened(true);
                      }}
                    >
                      
                    </StyledButton>
                  </Grid>
                  {isLoading && clockSpinner()}

                  <Grid item xs={12}>
                    {!isLoading && patientData.length === 0 && (
                      <Grid container alignItems="center" justify="center">
                        <Grid
                          item
                          xs={5}
                          md={5}
                          lg={6}
                          xl={6}
                          style={{
                            textAlign: 'center',
                          }}
                        >
                          <h1
                            style={{
                              fontWeight: 'bold',
                            }}
                          >
                             
                          </h1>
                        </Grid>
                        <Grid
                          item
                          xs={7}
                          md={7}
                          lg={6}
                          xl={6}
                          style={{ marginTop: '2em' }}
                        >
                          <img
                            src="/assets/image/pleaseSearching.png"
                            width="60%"
                            alt="searching"
                          />
                        </Grid>
                      </Grid>
                    )}
                    {!isLoading && patientData.length > 0 && (
                      <TableContainer style={{ marginTop: '1rem' }}>
                        <Table
                          style={{ minWidth: '600px', overflowX: 'scroll' }}
                        >
                          <TableHead>
                            <TableRow>
                              <TableCell component="td"></TableCell>
                              <TableCell component="td"></TableCell>
                              <TableCell component="td"></TableCell>
                              <TableCell component="td"></TableCell>
                              <TableCell component="td"></TableCell>
                            </TableRow>
                          </TableHead>
                          <TableBody>
                            {patientData.map((data) => (
                              <Fragment key={data.patientId}>
                                <TableRow>
                                  <TableCell component="th">
                                    {data.patientId}
                                  </TableCell>
                                  <TableCell component="th">
                                    {data.patientName}
                                  </TableCell>
                                  <TableCell component="th">
                                    {moment(data.patientBirth).format(
                                      'yyyy MM DD',
                                    )}
                                  </TableCell>
                                  <TableCell component="th">
                                    {data.patientAddr2 === undefined &&
                                      data.patientAddr1}
                                    {data.patientAddr2 !== undefined &&
                                      data.patientAddr1 +
                                        ' ' +
                                        data.patientAddr2}
                                  </TableCell>
                                  <TableCell component="th">
                                    <StyledButton
                                      bgColor="rgb(11, 83, 151)"
                                      color="white"
                                      onClick={() => {
                                        setUpdateOpened(true);
                                        setReadPatientData(data);
                                      }}
                                    >
                                      
                                    </StyledButton>
                                  </TableCell>
                                </TableRow>
                              </Fragment>
                            ))}
                          </TableBody>
                        </Table>
                      </TableContainer>
                    )}
                  </Grid>
                </Grid>
              </ContentContainer>
            </PageTransition>
          </Grid>
        </Grid>
      </main>
      {/*     */}
      <PatientDrawer
        isOpened={isOpened}
        setOpened={setOpened}
        setDisplay={setDisplay}
      />
      {/*     */}
      <PatientUpdateDrawer
        isUpdateOpened={isUpdateOpened}
        setUpdateOpened={setUpdateOpened}
        readPatientData={readPatientData}
        setDisplay={setDisplay}
      />
    </div>
  );
};

export default PatientPage;
