import React, { useState, useEffect, Fragment } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { DataGrid } from '@material-ui/data-grid';
import { useSnackbar } from 'notistack';
import { setPatient } from 'redux/features/diagnosis/diagnosisSlice';
import { getDiagnosisList } from 'apis/diagnosisAPI';
import HashSpinner from 'components/common/spinner/HashSpinner';
import StyledTypography from 'components/common/typography/StyledTypography';

const columns = [
  { field: 'diagId', headerName: '', width: 120, type: 'number' },
  { field: 'patientName', headerName: ' ', width: 150 },
  { field: 'patientBirth', headerName: '', width: 150 },
  { field: 'patientGender', headerName: '', width: 120 },
  { field: 'startDate', headerName: ' ', width: 200 },
  { field: 'visitPurpose', headerName: ' ', width: 700 },
];

/**
 * *        Template
 * @returns {JSX.Element} view
 * @author SUNG WOOK HWANG
 */
const DataTable = () => {
  const [isLoading, setLoading] = useState(true);
  const authInfo = useSelector((state) => state.common.loginInfo);
  const [diagnosisList, setDiagnosisList] = useState([]);
  const dispatch = useDispatch();
  const { enqueueSnackbar } = useSnackbar();

  const handleAlert = (variant, message) => {
    enqueueSnackbar(message, {
      variant,
    });
  };
  useEffect(() => {
    async function getDiagnosis() {
      try {
        const result = await getDiagnosisList(authInfo);

        const newResult = result.map((data, index) => ({ id: index, ...data }));
        setDiagnosisList(newResult);
        console.log(result);
        setLoading(false);
      } catch (error) {
        const { message } = error.response.data;
        handleAlert('error', message);
        setLoading(false);
      }
    }
    setTimeout(() => {
      getDiagnosis();
    }, 1000);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handleClick = (event) => {
    const { row } = event;
    // console.log('dataTable', event.row);
    dispatch(setPatient(row));
  };
  return (
    <Fragment>
      {isLoading && (
        <div
          style={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            width: '100vw',
            height: '80vh',
          }}
        >
          <HashSpinner />
        </div>
      )}
      {!isLoading && diagnosisList.length > 0 && (
        <div style={{ height: 500, width: '100%' }}>
          <DataGrid
            rows={diagnosisList}
            columns={columns}
            pageSize={5}
            onCellClick={handleClick}
          />
        </div>
      )}

      {!isLoading && diagnosisList.length === 0 && (
        <div
          style={{
            height: '80vh',
            width: '100%',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            flexDirection: 'column',
          }}
        >
          <div style={{ width: '100%', maxWidth: '500px' }}>
            <img src="/assets/image/404/3.png" alt="not found" width="100%" />
          </div>
          <StyledTypography
            variant="h4"
            component="h5"
            weight={7}
            style={{
              marginTop: '1rem',
            }}
          >
                   .
          </StyledTypography>
        </div>
      )}
    </Fragment>
  );
};

export default DataTable;
