import axios from 'axios';

/**
 * *  : MQTT     API
 * @param {PushMessageVO} pushMessage 
 * * !String topic
 * * !String priority
 * * !String message
 * * !LocalDateTime createdAt (new Date().toJSON()   )
 * @returns {Boolean} result
 * @throws
 * * HTTP Status 500 Internal Server Error
 * @author SUNG WOOK HWANG
 */
export const sendMqttMessage = async (pushMessage) => {
    const result = await axios.post('/mqtt', pushMessage);
    return result.data.data;
}

/**
 * *  :  Push    API
 * @param {string} topic (/hospitalCode/memberAuthority  )
 * @returns {List<PushMessageVO>} result
 * * !String topic
 * * !String priority
 * * !String message
 * * !LocalDateTime createdAt (const [year, date, day, time]   )
 * @throws
 * * HTTP Status 400 - Bad Request
 * * * status - "no_topics"
 * * * message - "   ."
 * * HTTP Status 404 - Not Found
 * * * status - "no_message"
 * * * message - "    ."
 */
export const getNotificationByTopic = async (topic) => {
    const result = await axios.get('/mqtt/notification', {
        params: {
            topic
        }
    });

    const data = result.data.data;
    return data;
}
