import React, { useState } from 'react';
import moment from 'moment';
import { useSelector } from 'react-redux';
import { registerReservationInfo } from 'apis/reservationAPI';
import { Grid } from '@material-ui/core';
import { useSnackbar } from 'notistack';
import StyledTypography from 'components/common/typography/StyledTypography';
import StyledInputBase from 'components/common/input/StyledInputBase';
import StyledButton from 'components/common/button/StyledButton';
import { sendMqttMessage } from 'apis/pushAPI';

/*
   (, , , )  
    
  reservationTime:   (, , ), ReservationDrawer props  
                       props 
  doctorInfo:  , ReservationDrawer props       props 
  patientInfo: ReservationDrawer setPatient  ReservationPatientListContainer  
                .  patientInfo  .
                    props .
  setCheckChange: false->  (SearchBox) true->  ?     
                    true      
*/

/*
  :

*/
const ReservationInfoContainer = (
  {
    reservationTime,
    doctorInfo,
    patientInfo,
    setOpened,
    setCheckChange,
    setAddDisplay,
  },
  props,
) => {
  /*
        

    : 
  */
  const [visitReason, setReason] = useState('');

  // ,true -> ( +  ),false
  const [buttonChange, setButtonChange] = useState(false);
  //     redux
  const loginInfo = useSelector((state) => state.common.loginInfo);

  //    onChange
  const visitReasonHandleChange = (event) => {
    setReason(event.target.value);
  };
  const { enqueueSnackbar } = useSnackbar();
  const handleAlert = (variant, message) => {
    enqueueSnackbar(message, {
      variant,
    });
  };

  /*
      ,      
    1)    
    2)   
    3) 1),2)   , 
         (  +  ) -> setButtonChange
          -> setCheckChange

    : 
  */
  const handleReservationClick = () => {
    //1)
    if (patientInfo.patient_name === '' && patientInfo.patient_birth === '') {
      handleAlert('error', ' .');
    } else {
      //2)
      if (visitReason === '') {
        handleAlert('error', '  .');
      } else {
        //3)
        setCheckChange(true);
        setButtonChange(true);
      }
    }
  };
  /*
             
    reservationInfo  props(reservationTime, patientInfo,doctorInfo) 
     ,  (visitReason) 
      ui toast calendar      
    1)  reservationInfo   dispatch 
    2)    -> setCheckChange
    3)   (  +  ) -> setButtonChange

    : 
  */
  const handleInsertInfoClick = async () => {
    try {
      const startDate = moment(reservationTime.start.toDate()).format(
        'yyyy-MM-DDTHH:mm',
      );
      const endDate = moment(reservationTime.end.toDate()).format(
        'yyyy-MM-DDTHH:mm',
      );
      const reservationInfo = {
        weekNo: reservationTime.weekNum,
        startDate,
        endDate,
        visitPurpose: visitReason,
        drOpinion: '',
        isPharmacy: false,
        isInjector: false,
        isDiagnosticTest: false,
        isVital: false,
        reservationStatus: 'RESERVATION_REGISTER',
        patientId: patientInfo.patientId,
        memberId: doctorInfo.memberId,
        hospitalCode: loginInfo.hospitalCode,
      };
      //1) db 
      const { data } = await registerReservationInfo(reservationInfo);
      console.log(data.data);
      //2)
      setCheckChange(false);
      //3)
      setButtonChange(false);
      handleAlert('success', ' .');

      // Push Alert . ()
      const sendPushMessage = {
        topic: `/${loginInfo.hospitalCode}/doctor`,
        priority: 'success',
        message: `${patientInfo.patientName}  ${doctorInfo.memberName}   .`,
      };

      await sendMqttMessage(sendPushMessage);

      setAddDisplay(true);
      setOpened(false);
    } catch (error) {
      console.log('error', error);
      const { message } = error.response.data;
      handleAlert('error', message);
      setCheckChange(false);
      setButtonChange(false);
    }
  };

  return (
    <Grid
      container
      spacing={2}
      style={{
        padding: '2rem',
      }}
    >
      <Grid
        item
        xs={3}
        style={{
          display: 'flex',
          alignItems: 'center',
        }}
      >
        <StyledTypography variant="h6" component="h5" weight={5}>
          
        </StyledTypography>
      </Grid>
      <Grid item xs={9}>
        <StyledInputBase readOnly value={patientInfo.patientName} />
      </Grid>
      <Grid
        item
        xs={3}
        style={{
          display: 'flex',
          alignItems: 'center',
        }}
      >
        <StyledTypography variant="h6" component="h5" weight={5}>
          
        </StyledTypography>
      </Grid>
      <Grid item xs={9}>
        <StyledInputBase readOnly value={patientInfo.patientBirth} />
      </Grid>
      <Grid
        item
        xs={3}
        style={{
          display: 'flex',
          alignItems: 'center',
        }}
      >
        <StyledTypography variant="h6" component="h5" weight={5}>
           
        </StyledTypography>
      </Grid>
      <Grid item xs={9}>
        <StyledInputBase readOnly value={reservationTime.date} />
      </Grid>
      <Grid
        item
        xs={3}
        style={{
          display: 'flex',
          alignItems: 'center',
        }}
      >
        <StyledTypography variant="h6" component="h5" weight={5}>
           
        </StyledTypography>
      </Grid>
      <Grid item xs={9}>
        <StyledInputBase readOnly value={reservationTime.startTime} />
      </Grid>
      <Grid
        item
        xs={3}
        style={{
          display: 'flex',
          alignItems: 'center',
        }}
      >
        <StyledTypography variant="h6" component="h5" weight={5}>
           
        </StyledTypography>
      </Grid>
      <Grid item xs={9}>
        <StyledInputBase readOnly value={reservationTime.endTime} />
      </Grid>
      <Grid
        item
        xs={3}
        style={{
          display: 'flex',
          alignItems: 'center',
        }}
      >
        <StyledTypography variant="h6" component="h5" weight={5}>
          
        </StyledTypography>
      </Grid>
      <Grid item xs={9}>
        <StyledInputBase readOnly value={doctorInfo.doctorRoom} />
      </Grid>
      <Grid
        item
        xs={3}
        style={{
          display: 'flex',
          alignItems: 'center',
        }}
      >
        <StyledTypography variant="h6" component="h5" weight={5}>
          
        </StyledTypography>
      </Grid>
      <Grid item xs={9}>
        <StyledInputBase readOnly value={doctorInfo.memberName} />
      </Grid>
      <Grid
        item
        xs={3}
        style={{
          display: 'flex',
          alignItems: 'center',
        }}
      >
        <StyledTypography variant="h6" component="h5" weight={5}>
           
        </StyledTypography>
      </Grid>
      <Grid item xs={9}>
        <StyledInputBase
          onChange={visitReasonHandleChange}
          value={visitReason}
        />
      </Grid>
      {buttonChange === false ? (
        <Grid item xs={12} style={{ textAlign: 'center', marginTop: '1.5em' }}>
          <StyledButton
            width="80%"
            onClick={handleReservationClick}
            style={{ backgroundColor: 'rgb(30, 51, 71)', color: 'white' }}
          >
            
          </StyledButton>
        </Grid>
      ) : (
        <Grid item xs={12} style={{ textAlign: 'center', marginTop: '1.5em' }}>
          <div style={{ marginBottom: '1em' }}>
            <StyledButton
              width="80%"
              style={{
                backgroundColor: '#a9e34b',
                color: 'white',
              }}
              onClick={() => {
                setCheckChange(false);
                setButtonChange(false);
              }}
            >
               
            </StyledButton>
          </div>
          <div>
            <StyledButton
              width="80%"
              style={{
                backgroundColor: 'rgb(30, 51, 71)',
                color: 'white',
              }}
              onClick={handleInsertInfoClick}
            >
              
            </StyledButton>
          </div>
        </Grid>
      )}
    </Grid>
  );
};

export default ReservationInfoContainer;
