import React, { Fragment, useState } from 'react';
import styled from 'styled-components';
import InputBase from '@material-ui/core/InputBase';
import IconButton from '@material-ui/core/IconButton';
import { useSnackbar } from 'notistack';
import AddIcon from '@material-ui/icons/Add';
import { createTodo } from 'apis/todoAPI';

const SearchContainer = styled.div`
  padding: 0.5rem;
  border-radius: 5px;
  display: flex;
  align-items: center;
  background-color: aliceblue;
`;

const SearchBase = styled(InputBase)`
  margin-left: 8px;
  flex: 1;
  font-family: 'Spoqa Han Sans Neo', 'Noto Sans KR', sans-serif;
  font-weight: 700;
`;

/**
 *     (TODO)   .
 *    .
 * - InputBox
 * @returns {JSX.Element}
 * @author HYEONG YUN KIM
 */
const InputBox = ({
  setTodoInput,
  placeholder,
  noRemove,
  memberName,
  memberId,
  hospitalCode,
  setChanged,
}) => {
  //  (setTodoInput)  inputVal .
  const [inputVal, setInputVal] = useState('');

  //  
  const { enqueueSnackbar } = useSnackbar();
  const handleAlert = (variant, message) => {
    enqueueSnackbar(message, {
      variant,
    });
  };
  //  (TODO)   
  const handleAdd = async (event) => {
    try {
      setTodoInput(inputVal);
      const sendInfo = {
        createdDate: new Date().toJSON(),
        todoContent: inputVal,
        memberId,
        hospitalCode,
        memberName,
      };
      if (inputVal === '') {
        handleAlert('error', '  .');
        return;
      }
      await createTodo(sendInfo);
      //  
      setInputVal('');
      handleAlert('success', ' .');
      //     .
      setChanged(true);
    } catch (error) {
      console.log(error.response.data);
    }
  };

  //       inputVal .
  const handleChange = (event) => {
    setInputVal(event.target.value);
  };

  const handleKeyPress = (event) => {
    const { key } = event;
    if (key === 'Enter' && inputVal) {
      setTodoInput(inputVal);
      if (noRemove === undefined) {
        setTimeout(() => {
          setInputVal('');
        }, 10);
      }
    } else if (key === 'Enter' && !inputVal) {
      handleAlert('error', '  .');
    }
  };

  return (
    <Fragment>
      <SearchContainer>
        <SearchBase
          value={inputVal}
          placeholder={placeholder}
          onChange={handleChange}
          onKeyPress={handleKeyPress}
        />
        <IconButton type="submit" onClick={handleAdd}>
          <AddIcon />
        </IconButton>
      </SearchContainer>
    </Fragment>
  );
};

export default InputBox;
