import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';

import {
  modifyReservationInfo,
  removeReservationInfo,
} from 'apis/reservationAPI';
import moment from 'moment';
import { Grid } from '@material-ui/core';
import { useSnackbar } from 'notistack';
import StyledTypography from 'components/common/typography/StyledTypography';
import StyledInputBase from 'components/common/input/StyledInputBase';
import StyledButton from 'components/common/button/StyledButton';
import { sendMqttMessage } from 'apis/pushAPI';

/**
 * *  :     
 *       ReservationDrawer   .
 * readPatient:     .
 * @param {object} state hello world
 * @returns {JSX.Element} view
 */

const ReservationReadContainer = ({
  setReadOpened,
  readPatient,
  setAddDisplay,
}) => {
  //      
  const loginInfo = useSelector((state) => state.common.loginInfo);

  const { enqueueSnackbar } = useSnackbar();
  const handleAlert = (variant, message) => {
    enqueueSnackbar(message, {
      variant,
    });
  };

  //    , readPatient.drOpinion     
  const [visitReason, setReason] = useState(readPatient.visitPurpose);
  // ,        
  const [checkPage, setCheckPage] = useState('');
  //      
  const [reservationTime, setReservationTime] = useState({
    day: '',
    startTime: '',
    endTime: '',
  });

  /* 
          
    : checkPage
  */
  const visitReasonHandleChange = (event) => {
    setReason(event.target.value);
  };

  /*
    readPatient(props)    .
     readPatient        
    reservationTime  .
    : reservationTime
  */
  useEffect(() => {
    const day = moment(readPatient.start).format('YYYY MM DD');
    const startTime = moment(readPatient.start).format('LT');
    const endTime = moment(readPatient.end).format('LT');
    setReservationTime({ day, startTime, endTime });
  }, [readPatient]);

  /*
       ,    
    1)    
    2)     
    3)   ( ) 
  */
  const updateReservationInfo = (id, changeVisitReason) => {
    //1)
    if (visitReason === readPatient.visitPurpose) {
      handleAlert('error', '  ');
    } else {
      //2)
      try {
        const updateInfo = {
          diagId: id,
          patientId: readPatient.patientId,
          memberId: readPatient.memberId,
          hospitalCode: loginInfo.hospitalCode,
          visitPurpose: changeVisitReason,
        };
        //3)
        const { data } = modifyReservationInfo(updateInfo);
        console.log(data.data);
        handleAlert('success', ' .');

        setCheckPage('UPDATE');
      } catch (error) {
        const { message } = error.response.data;
        handleAlert('error', message);
      }
    }
  };
  /*
        ,   
    1)    
    2)   ( )
  */
  const deleteReservationInfo = async (id) => {
    //1)
    try {
      const { data } = await removeReservationInfo(id);
      console.log(data.data);
      handleAlert('success', ' .');

      const sendPushMessage = {
        topic: `/${loginInfo.hospitalCode}/doctor`,
        priority: 'info',
        message: `${readPatient.patientName}   .`,
      };

      await sendMqttMessage(sendPushMessage);
    } catch (error) {
      const { message } = error.response.data;
      handleAlert('error', message);
      console.log('error', error);
    }
    //2)
    setCheckPage('REMOVE');
  };

  /*
       
    1)    
    2)   ( )
  */
  const resultUpdate = () => {
    return (
      <div style={{ textAlign: 'center' }}>
        <div>
          <img src="/assets/image/accept.png" alt="accept" />
        </div>
        <div>
          <h1 style={{ fontWeight: 'bold', marginBottom: '2em' }}>
             .
          </h1>
        </div>
        <div>
          <StyledButton
            width="60%"
            bgColor="#DDB892"
            color="white"
            onClick={() => {
              setAddDisplay(true);
              setReadOpened(false);
              setCheckPage('');
            }}
          >
             
          </StyledButton>
        </div>
      </div>
    );
  };
  /*
       
    1)    
    2)   ( )
  */
  const resultDelete = () => {
    return (
      <div style={{ textAlign: 'center' }}>
        <div>
          <img src="/assets/image/accept.png" alt="accept" />
        </div>
        <div>
          <h1 style={{ fontWeight: 'bold', marginBottom: '2em' }}>
             .
          </h1>
        </div>
        <div>
          <StyledButton
            width="60%"
            bgColor="#DDB892"
            color="white"
            onClick={() => {
              setAddDisplay(true);
              setReadOpened(false);
              setCheckPage('');
            }}
          >
             
          </StyledButton>
        </div>
      </div>
    );
  };
  //    
  const mainContent = () => {
    return (
      <Grid
        container
        spacing={2}
        style={{
          padding: '2rem',
        }}
      >
        <Grid
          item
          xs={3}
          style={{
            display: 'flex',
            alignItems: 'center',
          }}
        >
          <StyledTypography variant="h6" component="h5" weight={5}>
            
          </StyledTypography>
        </Grid>
        <Grid item xs={9}>
          <StyledInputBase readOnly value={readPatient.title} />
        </Grid>
        <Grid
          item
          xs={3}
          style={{
            display: 'flex',
            alignItems: 'center',
          }}
        >
          <StyledTypography variant="h6" component="h5" weight={5}>
            
          </StyledTypography>
        </Grid>
        <Grid item xs={9}>
          <StyledInputBase
            readOnly
            value={moment(readPatient.patientBirth).format('yyyy MM DD')}
          />
        </Grid>
        <Grid
          item
          xs={3}
          style={{
            display: 'flex',
            alignItems: 'center',
          }}
        >
          <StyledTypography variant="h6" component="h5" weight={5}>
             
          </StyledTypography>
        </Grid>
        <Grid item xs={9}>
          <StyledInputBase readOnly value={reservationTime.day} />
        </Grid>
        <Grid
          item
          xs={3}
          style={{
            display: 'flex',
            alignItems: 'center',
          }}
        >
          <StyledTypography variant="h6" component="h5" weight={5}>
             
          </StyledTypography>
        </Grid>
        <Grid item xs={9}>
          <StyledInputBase readOnly value={reservationTime.startTime} />
        </Grid>
        <Grid
          item
          xs={3}
          style={{
            display: 'flex',
            alignItems: 'center',
          }}
        >
          <StyledTypography variant="h6" component="h5" weight={5}>
             
          </StyledTypography>
        </Grid>
        <Grid item xs={9}>
          <StyledInputBase readOnly value={reservationTime.endTime} />
        </Grid>
        <Grid
          item
          xs={3}
          style={{
            display: 'flex',
            alignItems: 'center',
          }}
        >
          <StyledTypography variant="h6" component="h5" weight={5}>
            
          </StyledTypography>
        </Grid>
        <Grid item xs={9}>
          <StyledInputBase readOnly value={readPatient.doctorRoom} />
        </Grid>
        <Grid
          item
          xs={3}
          style={{
            display: 'flex',
            alignItems: 'center',
          }}
        >
          <StyledTypography variant="h6" component="h5" weight={5}>
            
          </StyledTypography>
        </Grid>
        <Grid item xs={9}>
          <StyledInputBase readOnly value={readPatient.doctorName} />
        </Grid>
        <Grid
          item
          xs={3}
          style={{
            display: 'flex',
            alignItems: 'center',
          }}
        >
          <StyledTypography variant="h6" component="h5" weight={5}>
             
          </StyledTypography>
        </Grid>
        <Grid item xs={9}>
          <StyledInputBase
            onChange={visitReasonHandleChange}
            value={visitReason}
          />
        </Grid>
        <Grid item xs={6} style={{ textAlign: 'center', marginTop: '1.5em' }}>
          <StyledButton
            width="80%"
            bgColor="#fb8500"
            color="white"
            onClick={() => {
              updateReservationInfo(readPatient.id, visitReason);
            }}
          >
            
          </StyledButton>
        </Grid>
        <Grid item xs={6} style={{ textAlign: 'center', marginTop: '1.5em' }}>
          <StyledButton
            width="80%"
            bgColor="#d90429"
            color="white"
            onClick={() => {
              deleteReservationInfo(readPatient.id);
            }}
          >
            
          </StyledButton>
        </Grid>
      </Grid>
    );
  };

  return (
    <div>
      {/* checkPage     ,  ,    */}
      {checkPage === '' && mainContent()}
      {checkPage === 'UPDATE' && resultUpdate()}
      {checkPage === 'REMOVE' && resultDelete()}
    </div>
  );
};

export default ReservationReadContainer;
