import axios from 'axios';

/**
 * *  :    (hospitalCode) TODO    API
 *
 * @param {String} from
 * @param {String} to
 * @returns coronaData
 * * [todos Entity]
 * * * !String todoContent
 * * * !String hospitalCode
 * * * !String createdDate
 * * * !int todo_id
 * @author HYEONG YUN KIM
 */
export const getCoronaData = async (from, to) => {
  return await axios.get(
    'https://api.covid19api.com/total/country/kr',
    {
      params: {
        from,
        to,
      },
    },
  );
};
