import React, { Fragment, useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { changePage, chaneInputVal } from "redux/features/reservation/reservationSlice";
import { SwipeableDrawer } from '@material-ui/core';
import { AiOutlineClose } from 'react-icons/ai';
import useWindowSize from 'hooks/useWindowSize';
import ResponsiveContainer from 'components/common/container/ResponsiveContainer';
import DrawerHeader from 'components/common/drawer/DrawerHeader';
import SearchBox from 'components/common/search/SearchBox';
import ReservationInfoContainer from './ReservationInfoContainer';
import ReservationPatientListContainer from "./ReservationPatientListContainer"

/*
  
isOpened :     
setOpened :       .
reservationTime : ,     .
doctorInfo:   
*/
/*

1) changePage -> (   ->    )
2) checkChange -> (  -> (?) )
3) patientInfoSetting -> patientInfo    
4) keywordSetting ->    
*/
const ReservationDrawer = ({
  isOpened,
  setOpened,
  reservationTime,
  doctorInfo,
  registerPageResult,
  setAddDisplay
}, props) => {
  /*
    .       
  : changePage
  */
  const page = useSelector((state) => state.reservation.pageStatus);
  const dispatch = useDispatch();

  const { breakpoint } = useWindowSize();

  

  /*
      
  : patientInfoSetting
  */
  const [patientInfo, setPatient] = useState({
    patient_id: '',
    patient_name: '',
    patient_gender: '',
    patient_birth: '',
  });
 
  /*
    false  (SearchBox), true  ? 
        
    : checkChange
  */
  const [checkChange, setCheckChange] = useState(false);
 
  // drawer   
  const toggleDrawer = (open) => (e) => {
    if (e && e.type === 'keydown' && (e.key === 'Tab' || e.key === 'Shift')) {
      return;
    }
    if (!open) {
      dispatch(changePage('INFO'));
    }
    setOpened(open);
  };

  /*
    x   drawer 
    dispatch  changePage INFO  
    page info     (  +  )    
    
    : changePage
  */
  const handleChangeCloseClick = () => {
    setOpened(false);
    dispatch(changePage('INFO'));
  };

  /*
     Drawer(ReservationDrawer)   ( )
    checkChange  (false) .
    patientInfo   .
    -->   ,        

    : patientInfoSetting, checkChange
  */
  useEffect(() => {
    setCheckChange(false);
    setPatient({
      patient_id: '',
      patient_name: '',
      patient_gender: '',
      patient_birth: '',
    });
  }, [isOpened]);

  /*
        
        ,     
    patientInfo   .

    : patientInfoSetting
  */
  const setPatientInfo = (patient) => {
     setPatient({
      patientId: patient.patientId,
      patientName: patient.patientName,
      patientGender: patient.patientGender,
      patientBirth: patient.patientBirth
    });
  };

  /*
    SearchBox    
    redux dispatch   
      ReservationPatientListContainer 
    SEARCH_INFO  
    ReservationInfoContainer -> ReservationPatientListContainer    .

    : keywordSetting, changePage
  */
  const setSearchVal = async(inputVal) => {
    dispatch(changePage('SEARCH_INFO'));
    dispatch(chaneInputVal(inputVal));
  };

  

  return (
    
    <Fragment>
      <SwipeableDrawer
        anchor="right"
        open={isOpened}
        onOpen={toggleDrawer(true)}
        onClose={toggleDrawer(false)}
      >
        <ResponsiveContainer breakpoint={breakpoint}>
          <DrawerHeader breakpoint={breakpoint}>
            <h1> </h1>
            <div>
              <AiOutlineClose
                size={32}
                // : changePage
                onClick={handleChangeCloseClick}
                style={{ cursor: 'pointer' }}
              />
            </div>
          </DrawerHeader>
          {checkChange === false ? (
            // : keywordSetting
            <SearchBox
              setSearchVal={setSearchVal}
              placeholder="  ."
            />
          ) : (
            <div style={{ display: 'flex', alignItems: 'center' }}>
              <div style={{ flex: 2 }}>
                <img
                  src="/assets/image/question.jpeg"
                  width="100%"
                  alt="confirmPicture"
                />
              </div>
              <div
                style={{
                  flex: 3,
                  alignItems: 'center',
                  marginLeft: '1em',
                }}
              >
                <div style={{ fontWeight: 'bold' }}>
                  <h1>  </h1>
                  <h1> ? </h1>
                </div>
              </div>
            </div>
          )}
          {/* changePage */}
          {page === 'INFO' && (
            //  
            <ReservationInfoContainer
              reservationTime={reservationTime}
              doctorInfo={doctorInfo}
              patientInfo={patientInfo}
              setOpened={setOpened}
              setCheckChange={setCheckChange}
              setAddDisplay={setAddDisplay}
            />
          ) }
          {page === 'SEARCH_INFO' && (
            //     (: patientInfoSetting)
          
            <ReservationPatientListContainer setPatientInfo={setPatientInfo} registerPageResult={registerPageResult}/> 
            
          )}
          


        </ResponsiveContainer>
      </SwipeableDrawer>
    </Fragment>
  );
};

export default ReservationDrawer;
