import axios from 'axios';

/**
 * *  :            API
 * @param {UserInfoVO} userInfo 
 * * !String authToken // JWT Token
 * * !int memberId
 * * !String memberEmail
 * * !String memberAuthority
 * * !String hospitalCode
 * 
 * @returns {List<DiagnosisListVO>} diagnosisList
 * * [Patients Entity]
 * * !int patientId 
 * * !String patientName
 * * !String patientGender
 * * !String patientBirth (LocalDateTime > String)
 * * [Diagnosis Entity]
 * * !int diagId
 * * !String startDate (LocalDateTime > String)
 * * !String visitPurpose ( )
 * 
 * @throws
 * * HTTP Status 400 - Bad Request
 * * * status - "no_user_info"
 * * * message - "UserInfoVO    ."
 * * HTTP Status 204 - No Content
 * >      throw
 * * HTTP Status 409 - Conflict Request
 * * * status - "no_patient_info"
 * * * message - "   ."
 */
export const getDiagnosisList = async (userInfo) => {
    const result = await axios.post('/diagnosis', userInfo);
    return result.data.data;
}

/**
 * *  :        API
 * @param {RegistDiagnosisVO} diagnosisInfo 
 * * !int patientId ( PK)
 * * !int memberId ( () PK)
 * * !int diagId ( PK)
 * * !String hospitalCode ( )
 * * !String drOpinion ( )
 * * List<MedicineVO> medicines ( )
 * * * int medicineId (Medicines  PK)
 * * * int medicineDose ( )
 * * * String medicineType ( )
 * * List<MedicineVO> injectors ( )
 * * List<Integer> diagnostics (  )
 * * VitalVO vital (  )
 * * * !int bloodPressure ()
 * * * !int pulse ()
 * * * !int respirationRate ( )
 * * * !int temperature ()
 * @returns {boolean} result (  )
 * 
 * @throws
 * * HTTP Status 400 - Bad Request
 * * * status : "no_diagnosis_info"
 * * * message : "    .  ."
 * * HTTP Status 409 - Conflict Request
 * * * status : "not_updated_diagnosis_info" || "not_updated_medicines_info" || "not_updated_injectors_info" || "not_updated_diagnostics_info" || "not_updated_vital_info"
 * * * message : "      .  ."
 */
export const registDiagnosisInfo = async (diagnosisInfo) => {
    const result = await axios.put('/diagnosis', diagnosisInfo);
    return result.data.data;
}

/**
 * *  :   (patientId)       API

 * @param {number} patientId 
 * @returns {List<DiagnosisHistoryVO>} data
 * * !String startDate //    (LocalDateTime > String)
 * * !String visitPurpose //   
 * * !String drOpinion //  
 * * List<MedicineRecordVO> medicines //   
 * * * !String medicineCode //  
 * * * !String medicineType //  
 * * * !String medicineUnit // , ,  ..
 * * * !String medicineExplain //    
 * * * !int medicineDose //  
 * * !List<MedicineRecordVO> injectors //   
 * * !List<DiagnosticTestRecordVO> diagnostics //   
 * * * [DiagnosisTestRecords Entity]
 * * * * double diagTestValue //    
 * * * * !String diagTestStatus //   
 * * * [DiagnosticInspections Entity]
 * * * * !String bundleCode //  
 * * * * !String bundleName //  
 * * * * !String presCode //  
 * * * * !String presName //  
 * * * * !String presUnit //  
 * * * * !double presLowerLimit //  
 * * * * double presUpperLimit //  
 * * VitalRecordsDTO vital //   
 * 
 * @throws
 * * HTTP Status 400 - Bad Request
 * * * status : "no_patient_id"
 * * * message : "   ."
 * * HTTP Status 204 - No Content
 * >         No Content
 * 
 */
export const showDiagnosisHistoryByPatientId = async (patientId) => {
    const result = await axios.get("/diagnosis/history", {
        params: {
            patientId
        }
    });
    return result.data.data;
}