import React, { Fragment} from 'react';
import moment from 'moment';
import StyledTypography from 'components/common/typography/StyledTypography';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';

/*
       .
  1) searchResults        
  2) setReadPatient     ( )      .
  3) pageResult     or    
*/
const PatientInfoListItem = ({ searchResults, setReadPatient, setReadOpened, pageResult }) => {

  /*
        , paient 
    setReadPatient patient .
    setReadOpened true  
    ReservationInfoListContainer   .
  */
  const handlePatientClick = (patient) => {
    setReadPatient(patient);
    setReadOpened(true);
  };



  //     . ( )
  const resultItem = (reservation) => {
      return (
        <div key={reservation.id}>
          <Divider variant="inset" component="li" />
          <ListItem
            alignItems="flex-start"
            style={{ cursor: 'pointer' }}
            onClick={() => handlePatientClick(reservation)}
          >
            <ListItemAvatar>
              <Avatar alt={reservation.title} src="" />
            </ListItemAvatar>
            <ListItemText
              primary={
                reservation.title +
                ' / ' +
                reservation.doctorRoom +
                ' ( ' +
                moment(reservation.start).format('LT') +
                ' ~ ' +
                moment(reservation.end).format('LT') +
                ' )'
              }
              secondary={
                <Fragment>
                  <StyledTypography
                    component="span"
                    variant="body2"
                    color="textPrimary"
                  >
                    {' ' +
                      reservation.patientBirth[0] +
                      '-' +
                      reservation.patientBirth[1] +
                      '-' +
                      reservation.patientBirth[2]}
                  </StyledTypography>
                </Fragment>
              }
            />
          </ListItem>
          <Divider variant="inset" component="li" />
        </div>
      );
    }
  ///  
  const resultSearch = () => {
    return (
          <div style={{ alignItems: 'center' }}>
            <div style={{ width: '80%' }}>
              <img
                src="/assets/image/pleaseSearching.png"
                width="100%"
                alt="confirmPicture"
              />
            </div>
            <div
              style={{
                alignItems: 'center',
              }}
            >
              <div
                style={{
                  fontWeight: 'bold',
                  textAlign: 'center',
                  marginTop: '1em',
                }}
              >
                <h1>  </h1>
              </div>
            </div>
          </div>
    )
  }

  //    .
  const resultNot = () => {
    return (
      <div style={{ alignItems: 'center' }}>
            <div style={{ width: '100%', textAlign: 'center' }}>
              <img
                src="/assets/image/notFound.png"
                width="100%"
                alt="confirmPicture"
              />
            </div>
            <div
              style={{
                alignItems: 'center',
              }}
            >
              <div
                style={{
                  fontWeight: 'bold',
                  textAlign: 'center',
                  marginTop: '1em',
                }}
              >
                <h1>   </h1>
              </div>
            </div>
          </div>
    )
  }
  return (
    <List>
      {searchResults.length > 0 &&
        searchResults.map((reservation) => 
          resultItem(reservation)
        )}
      {searchResults.length === 0 &&
        (pageResult === false ? (
        resultSearch()
        ) : (
          resultNot()
        ))}
    </List>
  );
};

export default PatientInfoListItem;
