import axios from 'axios';

/**
 * *  : hospitalCode       API
 * @param {String} hospitalCode
 * @returns {List<PatientsDTO>} patientList
 * * [Patients Entity]
 * * !int patientId
 * * !String patientName
 * * !String patientSsn;
 * * !String patientGender
 * * !String patientTel
 * * !String patientPostal
 * * !String patientAddr1
 * * String patientAddr2
 * * !int patientHeight
 * * !int patientWeight
 * * !LocalDateTime recentDate
 * * !LocalDate patientBirth
 * * !String hospitalCode
 * @author SI HYUN PARK
 */
export const getPatientsList = async (hospitalCode) => {
  return await axios.get(`/patient/search/${hospitalCode}`);

};

/**
 * *  :          API
 * @param {PatientSearchVO} searchInfo
 * * !String hospitalCode
 * * !String patientName
 * @returns {List<PatientsDTO>} patientSearchList
 * * [Patients Entity]
 * * !int patientId
 * * !String patientName
 * * !String patientSsn;
 * * !String patientGender
 * * !String patientTel
 * * !String patientPostal
 * * !String patientAddr1
 * * String patientAddr2
 * * !int patientHeight
 * * !int patientWeight
 * * !LocalDateTime recentDate
 * * !LocalDate patientBirth
 * * !String hospitalCode
 * @author SI HYUN PARK
 */
export const getSearchPatientList = async (searchInfo) => {
  return await axios.post('/patient/search', searchInfo);
};

