import { useState, useCallback } from 'react';
import moment from 'moment';

/**
 * [calInfo, sendCalInfo, getPrevWeek, getNextWeek, reset]
 * *     Custom Hook
 * @returns [calInfo : object, sendCalInfo : object , getPrevWeek : function, getNextWeek : function, reset : function]
 * * calInfo      (weekNo), startDate ( ), endDate( )    .
 * * sendCalInfo     YYYY-MM-DD    .
 * * getPrevWeek        .
 * * getNextWeek        .
 * * reset     .
 * @author SUNG WOOK HWANG
 */
const useCalendar = () => {
  const [calInfo, setCalInfo] = useState({
    weekNo: moment().week(),
    startDate: moment(moment().startOf('week').toDate()).format(
      'YYYY MM DD',
    ),
    endDate: moment(moment().endOf('week').toDate()).format('YYYY MM DD'),
  });

  const [sendCalInfo, setSendCalInfo] = useState({
    weekNo: moment().week(),
    startDate: moment(moment().startOf('week').toDate()).format('YYYY-MM-DD'),
    endDate: moment(moment().endOf('week').toDate()).format('YYYY-MM-DD'),
  });

  const getPrevWeek = useCallback(() => {
    setCalInfo((prevState) => {
      const newState = prevState;
      const { weekNo } = newState;
      return {
        weekNo: weekNo - 1,
        startDate: moment(
          moment()
            .week(weekNo - 1)
            .startOf('week')
            .toDate(),
        ).format('YYYY MM DD'),
        endDate: moment(
          moment()
            .week(weekNo - 1)
            .endOf('week')
            .toDate(),
        ).format('YYYY MM DD'),
      };
    });

    setSendCalInfo((prevState) => {
      const newState = prevState;
      const { weekNo } = newState;
      return {
        weekNo: weekNo - 1,
        startDate: moment(
          moment()
            .week(weekNo - 1)
            .startOf('week')
            .toDate(),
        ).format('YYYY-MM-DD'),
        endDate: moment(
          moment()
            .week(weekNo - 1)
            .endOf('week')
            .toDate(),
        ).format('YYYY-MM-DD'),
      };
    });
  }, []);

  const getNextWeek = useCallback(() => {
    setCalInfo((prevState) => {
      const newState = prevState;
      const { weekNo } = newState;
      return {
        weekNo: weekNo + 1,
        startDate: moment(
          moment()
            .week(weekNo + 1)
            .startOf('week')
            .toDate(),
        ).format('YYYY MM DD'),
        endDate: moment(
          moment()
            .week(weekNo + 1)
            .endOf('week')
            .toDate(),
        ).format('YYYY MM DD'),
      };
    });

    setSendCalInfo((prevState) => {
      const newState = prevState;
      const { weekNo } = newState;
      return {
        weekNo: weekNo + 1,
        startDate: moment(
          moment()
            .week(weekNo + 1)
            .startOf('week')
            .toDate(),
        ).format('YYYY-MM-DD'),
        endDate: moment(
          moment()
            .week(weekNo + 1)
            .endOf('week')
            .toDate(),
        ).format('YYYY-MM-DD'),
      };
    });
  }, []);

  const reset = useCallback(() => {
    setCalInfo({
      weekNo: moment().week(),
      startDate: moment(moment().startOf('week').toDate()).format(
        'YYYY MM DD',
      ),
      endDate: moment(moment().endOf('week').toDate()).format(
        'YYYY MM DD',
      ),
    });

    setSendCalInfo({
      weekNo: moment().week(),
      startDate: moment(moment().startOf('week').toDate()).format('YYYY-MM-DD'),
      endDate: moment(moment().endOf('week').toDate()).format('YYYY-MM-DD'),
    });
  }, []);

  return [calInfo, sendCalInfo, getPrevWeek, getNextWeek, reset];
};

export default useCalendar;
