import React, { Fragment, useEffect, useState } from 'react';
import {
  SwipeableDrawer,
  IconButton,
  TableContainer,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  Paper,
} from '@material-ui/core';
import { useDispatch, useSelector } from 'react-redux';
import { AiOutlineClose } from 'react-icons/ai';
import { useSnackbar } from 'notistack';
import { setDiagnosisHistoryDrawer } from 'redux/features/diagnosis/diagnosisSlice';
import useWindowSize from 'hooks/useWindowSize';
import ResponsiveContainer from 'components/common/container/ResponsiveContainer';
import DrawerHeader from 'components/common/drawer/DrawerHeader';
import StyledTypography from 'components/common/typography/StyledTypography';
import CollapsibleRows from '../container/CollapsibleRows';
import { showDiagnosisHistoryByPatientId } from 'apis/diagnosisAPI';
import HashSpinner from 'components/common/spinner/HashSpinner';

/**
 * *  :     Drawer 
 * @returns {JSX.Element} view
 * @author SUNG WOOK HWANG
 */
const DiagnosisHistoryDrawer = () => {
  //  breakpoint   Custom Hook
  const { breakpoint } = useWindowSize();

  // Spinner   
  const [isLoading, setLoading] = useState(false);

  //     
  const [historyData, setHistoryData] = useState([]);
  const dispatch = useDispatch();

  //  Drawer 
  const isOpened = useSelector(
    (state) => state.diagnosis.drawerStatus.diagnosisHistory,
  );
  const { enqueueSnackbar } = useSnackbar();

  const handleAlert = (variant, message) => {
    enqueueSnackbar(message, {
      variant,
    });
  };

  // Redux store   
  const patientInfo = useSelector((state) => state.diagnosis.patient);

  const toggleDrawer = (open) => (e) => {
    if (e && e.type === 'keydown' && (e.key === 'Tab' || e.key === 'Shift')) {
      return;
    }
    dispatch(setDiagnosisHistoryDrawer(open));
  };

  /**
   * *  : isOpened    side-effect
   */
  useEffect(() => {
    if (isOpened) {
      const { patientId } = patientInfo;
      async function getDiagnosisHistory(patientId) {
        try {
          const result = await showDiagnosisHistoryByPatientId(patientId);
          setHistoryData(result);
          setLoading(true);
        } catch (error) {
          const { message } = error.response.data;
          handleAlert('error', message);
          setLoading(true);
        }
      }

      getDiagnosisHistory(patientId);
    }
    return () => {
      setHistoryData([]);
      setLoading(false);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [isOpened]);

  return (
    <Fragment>
      <SwipeableDrawer
        anchor="right"
        open={isOpened}
        onOpen={toggleDrawer(true)}
        onClose={toggleDrawer(false)}
      >
        <ResponsiveContainer>
          <DrawerHeader
            breakpoint={breakpoint}
            style={{ padding: '0.5rem', backgroundColor: 'white', zIndex: 2 }}
          >
            <h1>  </h1>
            <div>
              <IconButton>
                <AiOutlineClose
                  size={32}
                  onClick={() => dispatch(setDiagnosisHistoryDrawer(false))}
                />
              </IconButton>
            </div>
          </DrawerHeader>
          {isLoading && historyData.length > 0 && (
            <TableContainer component={Paper}>
              <Table>
                <TableHead>
                  <TableRow>
                    <TableCell size="small" />
                    <TableCell>
                      <StyledTypography
                        variant="subtitle1"
                        component="h5"
                        weight={7}
                      >
                        
                      </StyledTypography>
                    </TableCell>
                    <TableCell>
                      <StyledTypography
                        variant="subtitle1"
                        component="h5"
                        weight={7}
                      >
                        
                      </StyledTypography>
                    </TableCell>
                    <TableCell>
                      {' '}
                      <StyledTypography
                        variant="subtitle1"
                        component="h5"
                        weight={7}
                      >
                        
                      </StyledTypography>
                    </TableCell>
                  </TableRow>
                </TableHead>

                <TableBody>
                  {historyData.map((rows) => (
                    <CollapsibleRows data={rows} />
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          )}
          {isLoading && historyData.length === 0 && (
            <div
              style={{
                display: 'flex',
                width: '100%',
                height: '80vh',
                justifyContent: 'center',
                alignItems: 'center',
                flexDirection: 'column',
              }}
            >
              <div style={{ maxWidth: '500px', width: '100%' }}>
                <img
                  src="/assets/image/404/5.png"
                  alt="not found"
                  width="100%"
                />
              </div>
              <StyledTypography variant="h4" component="h4" weight={6}>
                    .
              </StyledTypography>
            </div>
          )}
          {!isLoading && (
            <div
              style={{
                display: 'flex',
                width: '100%',
                height: '90vh',
                justifyContent: 'center',
                alignItems: 'center',
              }}
            >
              <HashSpinner />
            </div>
          )}
        </ResponsiveContainer>
      </SwipeableDrawer>
    </Fragment>
  );
};

export default DiagnosisHistoryDrawer;
