import React, { useState, useEffect, Fragment } from 'react';
import {
  Divider,
  Grid,
  Table,
  TableContainer,
  TableBody,
  TableCell,
  TableHead,
  TableRow,
  TableFooter,
  TablePagination,
  Paper,
  Button,
  IconButton,
  Avatar,
} from '@material-ui/core';
import { useSnackbar } from 'notistack';
import ContentContainer from 'components/common/container/ContentContainer';
import TitleHeader from 'components/common/header/TitleHeader';
import SearchBox from 'components/common/search/SearchBox';
import MemberDrawer from 'components/member/drawer/MemberDrawer';
import StyledButton from 'components/common/button/StyledButton';
import MemberUpdateDrawer from 'components/member/drawer/MemberUpdateDrawer';
import DeleteModal from 'components/member/modal/DeleteModal';
import { BiRefresh } from 'react-icons/bi';
import { FiRefreshCcw } from 'react-icons/fi';
import ResponsivePageHeader from 'components/common/header/ResponsivePageHeader';
import PageTransition from 'components/common/transition/PageTransition';
import {
  intializeMemberPw,
  showMembersListByHospitalCode,
  showMembersListByNameAndCode,
} from 'apis/memberAPI';
import { useSelector } from 'react-redux';
import PacmanSpinner from 'components/common/spinner/PacmanSpinner';

/**
 *         .
 *    .
 * * Sider
 * * Header
 * *   ( MemberDrawer, MemberUpdateDrawer, DeleteModal, Table, ColoredButton)
 * @returns {JSX.Element}
 * @author Jong Hyun Hong
 */
const MemberPage = () => {
  const [isOpened, setOpened] = useState(false);
  const [isUpdateOpened, setUpdateOpened] = useState(false);
  const [searchVal, setSearchVal] = useState('');

  const [selectedData, setSelectedData] = useState('');
  const [member, setMember] = useState([]);

  //page 
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(5);

  const [isOpenModal, setOpenModal] = useState(false);
  const { enqueueSnackbar } = useSnackbar();

  //
  const [isLoading, setLoading] = useState(true);

  // 
  const currentUser = useSelector((state) => state.common.loginInfo);

  //  
  async function showMember() {
    try {
      const { data, status } = await showMembersListByHospitalCode(
        currentUser.hospitalCode,
      );
      setMember(data.data);
      setLoading(false);
    } catch (error) {
      handleAlert('error', 'error:   ');
      setLoading(false);
    }
  }

  // 
  async function showSearchMember(searchKeyword) {
    if (searchKeyword.trim() === '' || searchKeyword === undefined) {
      return;
    }
    let memberSearchInfo = {
      memberName: searchKeyword,
      hospitalCode: currentUser.hospitalCode,
    };

    try {
      const { data, status } = await showMembersListByNameAndCode(
        memberSearchInfo,
      );
      if (data) {
        //   
        setMember(data.data);
      } else {
        //  
        handleAlert('error', ' .');
        return;
      }
    } catch (error) {
      console.log(error.response.data);
      handleAlert('error', 'error:  ');
    }
  }

  //  
  useEffect(() => {
    setLoading(true);
    showMember();
  }, []);

  const handleAlert = (variant, message) => {
    enqueueSnackbar(message, {
      variant,
    });
  };

  // ( )
  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  //  
  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  // 
  const handleInitPassword = async (memberData) => {
    try {
      const { data, status } = await intializeMemberPw(memberData.memberId);
      console.log(' : ', data);
      handleAlert(
        'success',
        `${memberData.memberName}  . (!@#douzone1234)`,
      );
    } catch (error) {
      handleAlert('error', '   ');
    }
  };

  // 
  const handleRefresh = () => {
    setPage(0);
    setSearchVal('');
    showMember();
  };

  // 
  useEffect(() => {
    console.log(searchVal);
    setPage(0);
    showSearchMember(searchVal);
    console.log(' -useEffect[searchVal]');
  }, [searchVal]);

  return (
    <div>
      {isLoading && (
        <div
          style={{
            width: '100%',
            height: '90vh',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
          }}
        >
          <PacmanSpinner />
        </div>
      )}
      {!isLoading && (
        <Fragment>
          <header
            style={{
              position: 'sticky',
              top: 0,
              backgroundColor: 'white',
              zIndex: 1,
            }}
          >
            <ResponsivePageHeader />
          </header>
          <main>
            <Grid container>
              <Grid item xs={12}>
                <PageTransition>
                  <ContentContainer>
                    <TitleHeader>
                      <span> | </span>
                      <span> </span>
                    </TitleHeader>
                    <br />
                    <Grid container>
                      <Grid item xs={9} lg={4}>
                        <SearchBox
                          setSearchVal={setSearchVal}
                          placeholder="  ."
                        />
                      </Grid>
                      <Grid item xs={1} lg={2}>
                        <IconButton
                          color="primary"
                          size="medium"
                          onClick={handleRefresh}
                          style={{ marginLeft: '20px', marginTop: '10px' }}
                        >
                          <FiRefreshCcw />
                        </IconButton>
                      </Grid>
                      <Grid
                        item
                        xs={2}
                        lg={6}
                        style={{
                          display: 'flex',
                          alignItems: 'center',
                          justifyContent: 'flex-end',
                        }}
                      >
                        {currentUser.memberAuthority === 'ROLE_DEVELOP' ||
                        currentUser.memberAuthority === 'ROLE_DIRECTOR' ? (
                          <StyledButton
                            width="100px"
                            bgColor="rgb(30, 51, 71)"
                            color="white"
                            onClick={() => setOpened((prevState) => !prevState)}
                          >
                            
                          </StyledButton>
                        ) : null}
                      </Grid>
                      <Grid item xs={12}>
                        <Paper>
                          <TableContainer
                            style={{ marginTop: '1rem', marginBottom: '1rem' }}
                          >
                            <Table
                              style={{ minWidth: '600px', overflowX: 'scroll' }}
                            >
                              <TableHead>
                                <TableRow>
                                  <TableCell
                                    component="td"
                                    style={{
                                      minWidth: '40px',
                                      overflowX: 'scroll',
                                    }}
                                  >
                                    
                                  </TableCell>
                                  <TableCell
                                    component="td"
                                    style={{
                                      minWidth: '50px',
                                      overflowX: 'scroll',
                                    }}
                                  ></TableCell>
                                  <TableCell
                                    component="td"
                                    style={{
                                      minWidth: '100px',
                                      overflowX: 'scroll',
                                    }}
                                  >
                                    {'&'}
                                  </TableCell>
                                  {currentUser.memberAuthority ===
                                    'ROLE_DEVELOP' ||
                                  currentUser.memberAuthority ===
                                    'ROLE_DIRECTOR' ? (
                                    <TableCell
                                      component="td"
                                      style={{
                                        minWidth: '100px',
                                        overflowX: 'scroll',
                                      }}
                                    >
                                      {'&'}
                                    </TableCell>
                                  ) : (
                                    <Fragment>
                                      <TableCell
                                        component="td"
                                        style={{
                                          minWidth: '100px',
                                          overflowX: 'scroll',
                                        }}
                                      >
                                        
                                      </TableCell>
                                      <TableCell
                                        component="td"
                                        style={{
                                          minWidth: '100px',
                                          overflowX: 'scroll',
                                        }}
                                      >
                                        
                                      </TableCell>
                                    </Fragment>
                                  )}
                                  <TableCell
                                    component="td"
                                    style={{
                                      minWidth: '200px',
                                      overflowX: 'scroll',
                                    }}
                                  >
                                    
                                  </TableCell>
                                  {currentUser.memberAuthority ===
                                    'ROLE_DEVELOP' ||
                                  currentUser.memberAuthority ===
                                    'ROLE_DIRECTOR' ? (
                                    <Fragment>
                                      <TableCell
                                        component="td"
                                        style={{
                                          minWidth: '100px',
                                          overflowX: 'scroll',
                                        }}
                                      ></TableCell>
                                      {/* <TableCell
                                        component="td"
                                        style={{
                                          minWidth: '100px',
                                          overflowX: 'scroll',
                                        }}
                                      ></TableCell> */}
                                      <TableCell
                                        component="td"
                                        style={{
                                          minWidth: '100px',
                                          overflowX: 'scroll',
                                        }}
                                      ></TableCell>
                                    </Fragment>
                                  ) : null}
                                </TableRow>
                              </TableHead>
                              <TableBody>
                                {member
                                  .slice(
                                    page * rowsPerPage,
                                    (page + 1) * rowsPerPage,
                                  )
                                  .map((data) => (
                                    <Fragment key={data.memberId}>
                                      {console.log('JONG HYUN HAT DA', data)}
                                      <TableRow hover="true">
                                        <TableCell component="th">
                                          {data.memberAuthority ===
                                            'ROLE_DEVELOP' && ''}
                                          {data.memberAuthority ===
                                            'ROLE_DIRECTOR' && ''}
                                          {data.memberAuthority ===
                                            'ROLE_DOCTOR' && ''}
                                          {data.memberAuthority ===
                                            'ROLE_INSPECTOR' && ''}
                                          {data.memberAuthority ===
                                            'ROLE_NURSE' && ''}
                                        </TableCell>
                                        <TableCell component="th">
                                          {data.memberImage !== null &&
                                          data.memberImage.trim() !== '' ? (
                                            <Avatar
                                              src={`${process.env.REACT_APP_SERVER_PATH}/image?path=${data.memberImage}`}
                                            />
                                          ) : (
                                            <Avatar
                                              style={{
                                                backgroundColor:
                                                  data.memberColor,
                                              }}
                                            />
                                          )}
                                        </TableCell>
                                        <TableCell component="th">
                                          <p
                                            style={{
                                              marginBottom: '0.325rem',
                                            }}
                                          >
                                            {data.memberName}
                                          </p>
                                          <Divider />
                                          <p
                                            style={{
                                              marginTop: '0.325rem',
                                            }}
                                          >
                                            {' '}
                                            {data.memberBirth}
                                          </p>
                                        </TableCell>
                                        {currentUser.memberAuthority ===
                                          'ROLE_DEVELOP' ||
                                        currentUser.memberAuthority ===
                                          'ROLE_DIRECTOR' ? (
                                          <TableCell component="th">
                                            <p
                                              style={{
                                                marginBottom: '0.325rem',
                                              }}
                                            >
                                              {data.memberEmail}
                                            </p>
                                            <Divider />
                                            <p
                                              style={{
                                                marginTop: '0.325rem',
                                              }}
                                            >
                                              {' '}
                                              {data.memberTel}
                                            </p>
                                          </TableCell>
                                        ) : (
                                          <Fragment>
                                            <TableCell component="th">
                                              {data.memberEmail}
                                            </TableCell>
                                            <TableCell component="th">
                                              {data.memberTel}
                                            </TableCell>
                                          </Fragment>
                                        )}
                                        <TableCell component="th">
                                          {data.memberAddr1} -{' '}
                                          {data.memberAddr2}
                                        </TableCell>
                                        {currentUser.memberAuthority ===
                                          'ROLE_DEVELOP' ||
                                        currentUser.memberAuthority ===
                                          'ROLE_DIRECTOR' ? (
                                          <Fragment>
                                            <TableCell component="th">
                                              <StyledButton
                                                bgColor="rgb(11, 83, 151)"
                                                color="white"
                                                onClick={() => {
                                                  setUpdateOpened(
                                                    (prevState) => !prevState,
                                                  );
                                                  setSelectedData(data);
                                                }}
                                              >
                                                
                                              </StyledButton>
                                            </TableCell>
                                            {/* <TableCell component="th">
                                              <StyledButton
                                                bgColor="rgba(165, 10, 17, 0.637)"
                                                color="white"
                                                onClick={() => {
                                                  setOpenModal(
                                                    (prevState) => !prevState,
                                                  );
                                                  setSelectedData(data);
                                                }}
                                              >
                                                
                                              </StyledButton>
                                            </TableCell> */}
                                            <TableCell component="th">
                                              <Button
                                                size="small"
                                                bgColor="rgba(11, 131, 31, 0.795)"
                                                color="white"
                                                onClick={() => {
                                                  handleInitPassword(data);
                                                }}
                                                endIcon={<BiRefresh />}
                                              >
                                                 
                                              </Button>
                                            </TableCell>
                                          </Fragment>
                                        ) : null}
                                      </TableRow>
                                    </Fragment>
                                  ))}
                              </TableBody>
                              <TableFooter>
                                <TableRow>
                                  <TablePagination
                                    count={member.length}
                                    page={page}
                                    rowsPerPage={rowsPerPage}
                                    onChangePage={handleChangePage}
                                    onChangeRowsPerPage={
                                      handleChangeRowsPerPage
                                    }
                                    rowsPerPageOptions={[5, 10, 25]}
                                  />
                                </TableRow>
                              </TableFooter>
                            </Table>
                          </TableContainer>
                        </Paper>
                      </Grid>
                    </Grid>

                    {/*  Drawer 
                  isOpened, setOpened: Drawer  
                  setMember: memberData     setter
                  currentUser:  (, ,  ) 
              */}
                    <MemberDrawer
                      isOpened={isOpened}
                      setOpened={setOpened}
                      currentUser={currentUser}
                      showMember={showMember}
                    />
                    {/*  Drawer 
                  isUpdateOpened, setUpdateOpened: Drawer 
                  memberData:   
                  setMember: memberData     setter
              */}
                    <MemberUpdateDrawer
                      isUpdateOpened={isUpdateOpened}
                      setUpdateOpened={setUpdateOpened}
                      memberData={selectedData}
                      currentUser={currentUser}
                      showMember={showMember}
                    />
                    {/*  Modal */}
                    <DeleteModal
                      isOpenModal={isOpenModal}
                      setOpenModal={setOpenModal}
                      memberData={selectedData}
                      showMember={showMember}
                    />
                  </ContentContainer>
                </PageTransition>
              </Grid>
            </Grid>
          </main>
        </Fragment>
      )}
    </div>
  );
};

export default MemberPage;
