import { createSlice } from '@reduxjs/toolkit';

export const noticeSlice = createSlice({
  name: 'notice',
  initialState: {
    activeStep: 'MAIN',
    noticeItem: [
      {
        notice_id: 1,
        notice_title: '1',
        notice_date: '6 21',
        notice_content: '1',
        notice_author: '',
        notice_head_text: '1',
        notice_head_image: '/assets/image/hospital.png',
      },
      {
        notice_id: 2,
        notice_title: '2',
        notice_date: '6 9',
        notice_content: '2',
        notice_author: '',
        notice_head_text: '2',
        notice_head_image: '/assets/image/hospital.png',
      },
      {
        notice_id: 3,
        notice_title: '3',
        notice_date: '6 9',
        notice_content: '3',
        notice_author: '',
        notice_head_text: '3',
        notice_head_image: '/assets/image/hospital.png',
      },
      {
        notice_id: 4,
        notice_title: '4',
        notice_date: '6 9',
        notice_content: '4',
        notice_author: '',
        notice_head_text: '4',
        notice_head_image: '/assets/image/hospital.png',
      },
      {
        notice_id: 5,
        notice_title: '5',
        notice_content: '5',
        notice_date: '6 9',
        notice_author: '',
        notice_head_text: '5',
        notice_head_image: '/assets/image/hospital.png',
      },
      {
        notice_id: 6,
        notice_title: '6',
        notice_content: '6',
        notice_date: '6 9',
        notice_author: '',
        notice_head_text: '6',
        notice_head_image: '/assets/image/hospital.png',
      },
    ],
    noticeCurrentIndex: 0,
  },
  reducers: {
    setActiveStep(state, action) {
      state.activeStep = action.payload;
    },
    addNoticeItem(state, action) {
      const newState = state.noticeItem;
      newState.push(action.payload);
      state.noticeItem = newState;
    },
    modifyNoticeItem(state, action) {
      const newState = state.noticeItem;
      const isExistedItem = newState.findIndex((data) =>
        data.notice_id === action.payload.notice_id ? true : false,
      );

      //      .
      if (isExistedItem !== -1) {
        newState[isExistedItem] = action.payload;
        state.noticeItem = newState;
      }
    },
    removeNoticeItem(state, action) {
      const filteredItem = state.noticeItem.filter((data) => {
        if (data.notice_id !== action.payload.notice_id) {
          return true;
        }
        return false;
      });
      state.noticeItem = filteredItem;
    },
    setNoticeCurrentIndex(state, action) {
      state.noticeCurrentIndex = action.payload;
    },
  },
});

export const {
  setActiveStep,
  addNoticeItem,
  modifyNoticeItem,
  removeNoticeItem,
  setNoticeCurrentIndex,
} = noticeSlice.actions;

export default noticeSlice.reducer;
