import React, { Fragment, useEffect, useState } from 'react';
import {
  SwipeableDrawer,
  Grid,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  TextField,
  Paper,
} from '@material-ui/core';
import StyledTypography from 'components/common/typography/StyledTypography';
import DrawerHeader from 'components/common/drawer/DrawerHeader';
import { AiOutlineClose } from 'react-icons/ai';
import { useSnackbar } from 'notistack';
import useWindowSize from 'hooks/useWindowSize';
import StyledInputBase from 'components/common/input/StyledInputBase';
import ResponsiveContainer from 'components/common/container/ResponsiveContainer';
import StyledButton from 'components/common/button/StyledButton';
import PostalCodeModal from '../modal/PostalCodeModal';
import { setModalStatus } from 'redux/features/member/memberSlice';
import { useDispatch, useSelector } from 'react-redux';
import {
  isExistedEmail,
  memberImageUpload,
  modifyMemberInfo,
} from 'apis/memberAPI';
import { v4 as uuid } from 'uuid';
import { CirclePicker } from 'react-color';
import ImageUploader from 'react-images-upload';

/**
 *  DRAWER      .
 *    .
 * * DRAWER (SwipeableDrawer)
 * * *   (PostalCodeModal)
 * * *   (ImageUploader)
 * * *   (CirclePicker)
 * @returns {JSX.Element}
 * @author Jong Hyun Hong
 */
const MemberUpdateDrawer = ({
  isUpdateOpened,
  setUpdateOpened,
  memberData,
  currentUser,
  showMember,
}) => {
  const { breakpoint } = useWindowSize();

  //     
  const [selectVal, setSelectVal] = useState('');

  //      
  const [isEmailChecked, setIsEmailChecked] = useState(false);

  //      
  const [memberInfo, setMemberInfo] = useState({});

  const { enqueueSnackbar } = useSnackbar();

  //     
  const [currentColor, setCurrentColor] = useState('');

  const handleAlert = (variant, message) => {
    enqueueSnackbar(message, {
      variant,
    });
  };

  //   
  const handleChange = (event) => {
    //     
    if (event.target.name === 'memberEmail') {
      setIsEmailChecked(false);
    }
    setMemberInfo({ ...memberInfo, [event.target.name]: event.target.value });
    console.log(event.target.name);
    console.log(' : ', event.target.value);
  };

  //    
  const onDrop = async (event, picture) => {
    if (event.length === 0 || picture.length === 0) {
      return;
    }

    try {
      let ext = '.jpg';

      if (event[0].type === 'image/jpeg') {
        ext = '.jpg';
      } else if (event[0].type === 'image/gif') {
        ext = '.gif';
      } else if (event[0].type === 'image/png') {
        ext = '.png';
      } else if (event[0].type === 'image/webp') {
        ext = '.webp';
      }

      const imageName = uuid() + ext;

      let imageInfo = {
        hospitalCode: currentUser.hospitalCode,
        imageName,
        base64Content: picture[0],
      };

      await memberImageUpload(imageInfo);
      const sendImageName = `/${imageInfo.hospitalCode}/${imageInfo.imageName}`;
      setMemberInfo((prevState) => ({
        ...prevState,
        memberImage: sendImageName,
      }));
      // setPictures(imageInfo);
    } catch (error) {
      // const { message } = error.response.data;

      handleAlert('error', '      .');
      return;
    }
  };

  //Drawer   
  useEffect(() => {
    if (isUpdateOpened) {
      setMemberInfo({
        memberId: memberData.memberId,
        memberName: memberData.memberName,
        memberTel: memberData.memberTel,
        memberAuthority: memberData.memberAuthority,
        memberPostal: memberData.memberPostal,
        memberAddr1: memberData.memberAddr1,
        memberAddr2: memberData.memberAddr2,
        memberColor: memberData.memberColor,
        memberEmail: memberData.memberEmail,
        memberImage: memberData.memberImage,
        memberIntroduction: memberData.memberIntroduction,
      });
      console.log('updateOpened', memberData);
      setSelectVal(memberData.memberAuthority);
    }
  }, [isUpdateOpened]);

  const dispatch = useDispatch();
  const isModalOpened = useSelector((state) => state.member.modalStatus);
  const { member_postal, member_addr1 } = useSelector(
    (state) => state.member.addressInfo,
  );

  //    
  useEffect(() => {
    console.log('currentCOlor', currentColor);
    setMemberInfo({
      ...memberInfo,
      memberColor: currentColor,
    });
  }, [currentColor]);

  //    
  useEffect(() => {
    console.log('memberAuthority', selectVal);
    setMemberInfo({
      ...memberInfo,
      memberAuthority: selectVal,
    });
  }, [selectVal]);

  //      
  useEffect(() => {
    setMemberInfo({
      ...memberInfo,
      memberPostal: member_postal,
      memberAddr1: member_addr1,
    });
  }, [member_postal, member_addr1]);

  // ''   
  const handleSubmit = async (event) => {
    event.preventDefault();
    try {
      //   
      const regExpEmail =
        /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;

      //  
      const regExpTel =
        /^(01[016789]{1}|02|0[3-9]{1}[0-9]{1})-?[0-9]{3,4}-?[0-9]{4}$/;

      const isValidEmail = regExpEmail.test(memberInfo.memberEmail);
      const isValidTel = regExpTel.test(memberInfo.memberTel);

      //  
      let sameEmail = false;
      //  
      if (memberInfo.memberEmail === memberData.memberEmail) {
        sameEmail = true;
      }
      console.log('  : ', isEmailChecked);

      if (!sameEmail) {
        if (!isEmailChecked) {
          handleAlert('error', '   .');
          return;
        }
      } else if (!isValidEmail) {
        handleAlert('error', '   .');
        return;
      } else if (memberInfo.memberName === '') {
        handleAlert('error', ' .');
        return;
      } else if (!isValidTel) {
        handleAlert(
          'error',
          '  .(  ' - ' )',
        );
        return;
      } else if (
        memberInfo.memberPostal === '' ||
        memberInfo.memberAddr1 === ''
      ) {
        handleAlert('error', ' .   .');
        return;
      } else if (memberInfo.memberAddr2 === '') {
        handleAlert('error', ' .  .');
        return;
      } else if (memberInfo.memberColor === '') {
        handleAlert('error', ' .  .');
        return;
      }
      console.log('modifyInfo', memberInfo);
      await modifyMemberInfo(memberInfo);

      handleAlert('success', '   .');
      showMember();
      setUpdateOpened(false);
    } catch (error) {
      const { message } = error.response.data;

      if (message === undefined) {
        handleAlert(
          'error',
          '       .',
        );
        return;
      }

      handleAlert('error', message);
      return;
    }
  };

  // 
  const handleEmailChecked = async () => {
    if (memberInfo.memberEmail === '') {
      handleAlert('error', ' .');
      return;
    }

    // 
    const regExpEmail =
      /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;

    const isValidEmail = regExpEmail.test(memberInfo.memberEmail);
    if (!isValidEmail) {
      handleAlert('error', '   .');
      return;
    }

    try {
      if (memberInfo.memberEmail !== memberData.memberEmail) {
        const { data } = await isExistedEmail(memberInfo.memberEmail);
        console.log(' : ', data);
        handleAlert('success', '  .');
      }
      setIsEmailChecked(true);
    } catch (error) {
      handleAlert('error', '  .   .');
      return;
    }
  };

  //   
  const colorChange = (color, event) => {
    if (color) {
      setCurrentColor(color.hex);
      console.log(color.hex);
    }
  };

  useEffect(() => {
    if (breakpoint !== undefined) {
      console.log('Current breakpoint is', breakpoint);
    }
  }, [breakpoint]);

  const toggleDrawer = (open) => (e) => {
    if (e && e.type === 'keydown' && (e.key === 'Tab' || e.key === 'Shift')) {
      return;
    }
    setUpdateOpened(open);
  };

  return (
    <Fragment>
      <SwipeableDrawer
        anchor="right"
        open={isUpdateOpened}
        onOpen={toggleDrawer(true)}
        onClose={toggleDrawer(false)}
      >
        <form onSubmit={handleSubmit}>
          <ResponsiveContainer breakpoint={breakpoint}>
            <DrawerHeader breakpoint={breakpoint}>
              <h1>   - {memberData.memberName}</h1>
              <div>
                <AiOutlineClose
                  size={32}
                  onClick={() => setUpdateOpened(false)}
                />
              </div>
            </DrawerHeader>

            <Grid container spacing={1} style={{ padding: '1rem' }}>
              <Grid
                item
                xs={3}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                }}
              >
                <StyledTypography variant="h6" component="h5" weight={5}>
                  
                </StyledTypography>
              </Grid>
              <Grid item xs={9}>
                <FormControl style={{ width: '100%' }} variant="outlined">
                  <InputLabel id="form-label"></InputLabel>
                  <Select
                    labelId="form-label"
                    id="select-label"
                    label=""
                    value={selectVal}
                  >
                    <MenuItem
                      value="ROLE_DOCTOR"
                      onClick={() => setSelectVal('ROLE_DOCTOR')}
                    >
                      
                    </MenuItem>
                    <MenuItem
                      value="ROLE_NURSE"
                      onClick={() => setSelectVal('ROLE_NURSE')}
                    >
                      
                    </MenuItem>
                    <MenuItem
                      value="ROLE_INSPECTOR"
                      onClick={() => setSelectVal('ROLE_INSPECTOR')}
                    >
                      
                    </MenuItem>
                  </Select>
                </FormControl>
              </Grid>
              <Grid
                item
                xs={3}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                }}
              >
                <StyledTypography variant="h6" component="h5" weight={5}>
                  
                </StyledTypography>
              </Grid>
              <Grid item xs={7}>
                <StyledInputBase
                  name="memberEmail"
                  onChange={handleChange}
                  value={memberInfo.memberEmail || ''}
                />
              </Grid>
              <Grid
                item
                xs={2}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                }}
              >
                <StyledButton
                  bgColor="lightgray"
                  onClick={() => handleEmailChecked()}
                >
                   
                </StyledButton>
              </Grid>
              <Grid
                item
                xs={3}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                }}
              >
                <StyledTypography variant="h6" component="h5" weight={5}>
                  
                </StyledTypography>
              </Grid>
              <Grid item xs={9}>
                <StyledInputBase
                  name="memberName"
                  value={memberInfo.memberName || ''}
                  onChange={handleChange}
                />
              </Grid>
              <Grid
                item
                xs={3}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                }}
              >
                <StyledTypography variant="h6" component="h5" weight={5}>
                  
                </StyledTypography>
              </Grid>
              <Grid item xs={9}>
                <StyledInputBase
                  name="memberTel"
                  value={memberInfo.memberTel || ''}
                  onChange={handleChange}
                />
              </Grid>
              <Grid
                item
                xs={12}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                }}
              >
                <StyledTypography variant="h6" component="h5" weight={5}>
                  
                </StyledTypography>
              </Grid>

              <Grid item xs={9}>
                <StyledInputBase
                  name="memberPostal"
                  placeholder=" "
                  value={memberInfo.memberPostal || ''}
                  onChange={handleChange}
                  readOnly
                />
              </Grid>
              <Grid
                item
                xs={3}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                }}
              >
                <StyledButton
                  bgColor="lightgray"
                  onClick={() => dispatch(setModalStatus(!isModalOpened))}
                >
                   
                </StyledButton>
              </Grid>
              <Grid item xs={12}>
                <StyledInputBase
                  name="memberAddr1"
                  placeholder=""
                  value={memberInfo.memberAddr1 || ''}
                  onChange={handleChange}
                  readOnly
                />
              </Grid>
              <Grid item xs={12}>
                <StyledInputBase
                  name="memberAddr2"
                  placeholder="  ."
                  value={memberInfo.memberAddr2 || ''}
                  onChange={handleChange}
                />
              </Grid>
            </Grid>

            <div>
              <StyledTypography variant="h6" component="h5" weight={5}>
                
              </StyledTypography>
              <ImageUploader
                name="memberImage"
                withIcon={true}
                buttonText=" ."
                onChange={onDrop}
                imgExtension={['.jpg', '.png', '.jpeg']}
                fileSizeError="  . (5242880)"
                fileTypeError=" ."
                label="  : 5mb, : jpg, png "
                maxFileSize={5242880}
                singleImage
                withPreview
              />
            </div>
            <div>
              <StyledTypography variant="h6" component="h5" weight={5}>
                
              </StyledTypography>
              <TextField
                name="memberIntroduction"
                value={memberInfo.memberIntroduction || ''}
                onChange={handleChange}
                label="  ."
                multiline
                rows={4}
                variant="outlined"
                fullWidth
              />
            </div>
            <div
              style={{
                marginTop: 15,
                display: 'flex',
                alignItems: 'center',
              }}
            >
              <StyledTypography
                variant="h6"
                component="h5"
                weight={5}
                style={{ marginRight: 15 }}
              >
                
              </StyledTypography>
              <CirclePicker onChange={colorChange} />
              <Paper
                style={{
                  width: 60,
                  height: 60,
                  marginLeft: 30,
                  backgroundColor: memberInfo.memberColor || '',
                }}
              />
            </div>

            <div
              style={{
                position: 'fixed',
                bottom: 30,
                right: 20,
                display: 'flex',
                justifyContent: 'flex-end',
              }}
            >
              <StyledButton type="submit" bgColor="lightgray">
                 
              </StyledButton>
            </div>
            <PostalCodeModal />
          </ResponsiveContainer>
        </form>
      </SwipeableDrawer>
    </Fragment>
  );
};

export default MemberUpdateDrawer;
