import React, { Fragment, useEffect, useState } from 'react';
import {
  SwipeableDrawer,
  Grid,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  TextField,
  Paper,
} from '@material-ui/core';
import { AiOutlineClose } from 'react-icons/ai';
import { useSelector, useDispatch } from 'react-redux';
import {
  setModalStatus,
  setAddressInfo,
} from 'redux/features/member/memberSlice';
import StyledTypography from 'components/common/typography/StyledTypography';
import DrawerHeader from 'components/common/drawer/DrawerHeader';

import useWindowSize from 'hooks/useWindowSize';
import StyledInputBase from 'components/common/input/StyledInputBase';
import ResponsiveContainer from 'components/common/container/ResponsiveContainer';
import StyledButton from 'components/common/button/StyledButton';
import PostalCodeModal from '../modal/PostalCodeModal';
import { KeyboardDatePicker } from '@material-ui/pickers';
import ImageUploader from 'react-images-upload';
import StyledContainer from 'components/common/container/StyledContainer';
import { IoManOutline, IoWomanOutline } from 'react-icons/io5';
import { useSnackbar } from 'notistack';
import { addMember, memberImageUpload, isExistedEmail } from 'apis/memberAPI';
import { CirclePicker } from 'react-color';

/**
 *  DRAWER       .
 *    .
 * * DRAWER (SwipeableDrawer)
 * * *   (PostalCodeModal)
 * * *   (IoManOutline, IoWomanOutline)
 * * *   (KeyboardDatePicker)
 * * *   (ImageUploader)
 * * *   (CirclePicker)
 * @returns {JSX.Element}
 * @author Jong Hyun Hong
 */
const MemberDrawer = ({ isOpened, setOpened, currentUser, showMember }) => {
  const { breakpoint } = useWindowSize();

  //     
  const [selectVal, setSelectVal] = useState('');

  //      
  const [isEmailChecked, setIsEmailChecked] = useState(false);

  //     
  const [memberEmail, setMemberEmail] = useState('');

  //    ,  (      )
  const [keyboardDate, handleKeyDateChange] = useState(new Date());
  const [dateErrorMessage, setDateErrorMessage] = useState('');

  //     
  const [currentColor, setCurrentColor] = useState('');

  //  VO   
  const [pictures, setPictures] = useState('');
  const { enqueueSnackbar } = useSnackbar();

  //Drawer on/off ( )
  useEffect(() => {
    setPictures('');
    setMemberEmail('');
    setSelectedGender({
      female: false,
      male: false,
    });
    setCurrentColor('');
  }, [isOpened]);

  const handleAlert = (variant, message) => {
    enqueueSnackbar(message, {
      variant,
    });
  };

  //   
  const colorChange = (color, event) => {
    if (color) {
      setCurrentColor(color.hex);
      console.log(color.hex);
    }
  };

  //    
  const onDrop = async (event, picture) => {
    if (event.length === 1) {
      let imageName = '';
      imageName = event[0].name;
      let imageInfo = {
        hospitalCode: currentUser.hospitalCode,
        imageName,
        base64Content: picture[0],
      };
      setPictures(imageInfo);
      console.log(imageInfo);
      // const { data, status } = await memberImageUpload(pictures);
      // console.log(data);
    } else {
      // 
      console.log('', picture);
      setPictures('');
    }
  };

  const dispatch = useDispatch();

  const isModalOpened = useSelector((state) => state.member.modalStatus);

  const { member_postal, member_addr1 } = useSelector(
    (state) => state.member.addressInfo,
  );

  const [selectedGender, setSelectedGender] = useState({
    male: false,
    female: false,
  });

  //   
  const handleChangeGender = (name) => {
    console.log(name);
    if (name === 'male') {
      //
      setSelectedGender({
        female: false,
        male: true,
      });
    } else {
      //
      setSelectedGender({
        male: false,
        female: true,
      });
    }
  };

  //drawer on/off   
  useEffect(() => {
    dispatch(
      setAddressInfo({
        member_postal: '',
        member_addr1: '',
      }),
    );
  }, [dispatch]);

  useEffect(() => {
    if (breakpoint !== undefined) {
      console.log('Current breakpoint is', breakpoint);
    }
  }, [breakpoint]);

  //   
  const handleDateError = (data, error) => {
    if (data) {
      console.log(data);
      setDateErrorMessage(data);
    } else {
      console.log('  .');
      setDateErrorMessage('');
    }
  };

  //fuction(open){function(e){}}
  const toggleDrawer = (open) => (e) => {
    if (e && e.type === 'keydown' && (e.key === 'Tab' || e.key === 'Shift')) {
      return;
    }
    setOpened(open);
  };

  // 
  const handleEmailChecked = async () => {
    if (memberEmail === '') {
      handleAlert('error', ' .');
      return;
    }

    // 
    const regExpEmail =
      /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;

    const isValidEmail = regExpEmail.test(memberEmail);
    if (!isValidEmail) {
      handleAlert('error', '   .');
      return;
    }
    try {
      const { data, status } = await isExistedEmail(memberEmail);
      console.log(' : ', data);
      handleAlert('success', '  .');
      setIsEmailChecked(true);
    } catch (error) {
      handleAlert('error', '  .   .');
      return;
    }
  };

  // input 
  const handleChange = (event) => {
    setMemberEmail(event.target.value);
  };

  //   
  useEffect(() => {
    setIsEmailChecked(false);
  }, [memberEmail]);

  //  (submit)
  const handleSubmit = async (event) => {
    event.preventDefault();

    //   
    const regExpEmail =
      /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;

    //   
    // ,   1 ,  2   8  
    const regExpPw =
      /(?=.*\d{1,50})(?=.*[~`!@#$%\^&*()-+=]{1,50})(?=.*[a-zA-Z]{2,50}).{8,50}$/;

    //  
    const regExpTel =
      /^(01[016789]{1}|02|0[3-9]{1}[0-9]{1})-?[0-9]{3,4}-?[0-9]{4}$/;

    let memberAuthority = '';
    if (selectVal === '') {
      memberAuthority = 'ROLE_DIRECTOR';
    } else if (selectVal === '') {
      memberAuthority = 'ROLE_DOCTOR';
    } else if (selectVal === '') {
      memberAuthority = 'ROLE_NURSE';
    } else if (selectVal === '') {
      memberAuthority = 'ROLE_INSPECTOR';
    }

    const memberEmail = event.target.memberEmail.value;
    const memberPw = event.target.memberPassword.value;
    const memberName = event.target.memberName.value;
    const memberTel = event.target.memberTel.value;
    const memberBirth = event.target.memberBirth.value;
    const memberPostal = event.target.memberAddress1.value;
    const memberAddr1 = event.target.memberAddress2.value;
    const memberAddr2 = event.target.memberAddress3.value;
    let memberImage = '';
    const memberIntroduction = event.target.memberIntroduce.value;
    const memberColor = currentColor;

    let gender = '';
    if (selectedGender['male'] === true) {
      gender = 'male';
    } else if (selectedGender['female'] === true) {
      gender = 'female';
    }
    const memberGender = gender;

    // 
    const isValidEmail = regExpEmail.test(memberEmail);
    const isValidPW = regExpPw.test(memberPw);
    const isValidTel = regExpTel.test(memberTel);

    if (!isEmailChecked) {
      handleAlert('error', '   .');
      return;
    } else if (!isValidEmail) {
      handleAlert('error', '   .');
      return;
    } else if (!isValidPW) {
      handleAlert(
        'error',
        ' ,   1 ,  2    8   .',
      );
      return;
    } else if (memberGender === '') {
      handleAlert('error', ' .');
      return;
    } else if (memberName === '') {
      handleAlert('error', ' .');
      return;
    } else if (!isValidTel) {
      handleAlert(
        'error',
        '  .(  ' - ' )',
      );
      return;
    } else if (memberBirth === '') {
      handleAlert('error', '  .');
      return;
    } else if (!(dateErrorMessage === '')) {
      console.log(dateErrorMessage);
      handleAlert(
        'error',
        ` ${dateErrorMessage}   .`,
      );
      return;
    } else if (memberPostal === '' || memberAddr1 === '') {
      handleAlert('error', ' .   .');
      return;
    } else if (memberAddr2 === '') {
      handleAlert('error', ' .  .');
      return;
    } else if (memberColor === '') {
      handleAlert('error', '  .  .');
      return;
    }

    //pictures  
    if (pictures !== '') {
      const { data, status } = await memberImageUpload(pictures);
      console.log(' : ', data);
      memberImage = data;
    }

    try {
      let memberInfo = {
        hospitalCode: currentUser.hospitalCode,
        memberPw,
        memberBirth,
        memberEmail,
        memberGender,
        memberName,
        memberTel,
        memberAuthority,
        memberPostal,
        memberAddr1,
        memberAddr2,
        memberEnabled: 1,
        memberImage,
        memberIntroduction,
        memberColor,
      };
      console.log(memberInfo);
      const { data, status } = await addMember(memberInfo);
      console.log(data);
    } catch (error) {
      handleAlert('error', '  .');
    }
    showMember();
    handleAlert('success', ' .');
    setOpened(false);
  };

  return (
    <Fragment>
      <SwipeableDrawer
        anchor="right"
        open={isOpened}
        onOpen={toggleDrawer(true)}
        onClose={toggleDrawer(false)}
      >
        <form onSubmit={handleSubmit}>
          <ResponsiveContainer breakpoint={breakpoint}>
            <DrawerHeader breakpoint={breakpoint}>
              <h1> </h1>
              <div>
                <AiOutlineClose size={32} onClick={() => setOpened(false)} />
              </div>
            </DrawerHeader>

            <Grid container spacing={1} style={{ padding: '1rem' }}>
              <Grid
                item
                xs={3}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                }}
              >
                <StyledTypography variant="h6" component="h5" weight={5}>
                  
                </StyledTypography>
              </Grid>
              <Grid item xs={9}>
                <FormControl style={{ width: '100%' }} variant="outlined">
                  <InputLabel id="form-label"></InputLabel>
                  <Select
                    labelId="form-label"
                    id="select-label"
                    label=""
                    onChange={(e) => setSelectVal(e.target.value)}
                    value={selectVal}
                  >
                    <MenuItem value="" onClick={() => setSelectVal('')}>
                      
                    </MenuItem>
                    <MenuItem
                      value=""
                      onClick={() => setSelectVal('')}
                    >
                      
                    </MenuItem>
                    <MenuItem
                      value=""
                      onClick={() => setSelectVal('')}
                    >
                      
                    </MenuItem>
                  </Select>
                </FormControl>
              </Grid>
              <Grid
                item
                xs={3}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                }}
              >
                <StyledTypography variant="h6" component="h5" weight={5}>
                  
                </StyledTypography>
              </Grid>
              <Grid item xs={7}>
                <StyledInputBase
                  name="memberEmail"
                  onChange={handleChange}
                  value={memberEmail}
                />
              </Grid>
              <Grid
                item
                xs={2}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                }}
              >
                <StyledButton
                  bgColor="lightgray"
                  onClick={() => handleEmailChecked()}
                >
                   
                </StyledButton>
              </Grid>
              <Grid
                item
                xs={3}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                }}
              >
                <StyledTypography variant="h6" component="h5" weight={5}>
                  
                </StyledTypography>
              </Grid>
              <Grid item xs={9}>
                <StyledInputBase name="memberPassword" type="password" />
              </Grid>
              <Grid
                item
                xs={3}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                }}
              >
                <StyledTypography variant="h6" component="h5" weight={5}>
                  
                </StyledTypography>
              </Grid>
              <Grid item xs={9}>
                <div
                  style={{
                    display: 'flex',
                    maxWidth: '400px',
                    height: '100px',
                  }}
                >
                  <StyledContainer
                    bgColor="rgb(62,63,70)"
                    style={{
                      display: 'flex',
                      alignItems: 'center',
                    }}
                    onClick={() => handleChangeGender('male')}
                  >
                    {selectedGender.male && (
                      <Fragment>
                        <IoManOutline color="rgb(244,213,51)" size={64} />
                        <div
                          style={{
                            display: 'flex',
                            justifyContent: 'center',
                            flex: 1,
                          }}
                        >
                          <StyledTypography
                            variant="subtitle1"
                            weight={7}
                            style={{
                              color: 'rgb(244,213,51)',
                              fontFamily: 'Lato',
                            }}
                          >
                             (Male)
                          </StyledTypography>
                        </div>
                      </Fragment>
                    )}
                    {!selectedGender.male && (
                      <Fragment>
                        <IoManOutline color="white" size={64} />
                        <div
                          style={{
                            display: 'flex',
                            justifyContent: 'center',
                            flex: 1,
                          }}
                        >
                          <StyledTypography
                            variant="subtitle1"
                            weight={7}
                            style={{
                              color: 'white',
                              fontFamily: 'Lato',
                            }}
                          >
                             (Male)
                          </StyledTypography>
                        </div>
                      </Fragment>
                    )}
                  </StyledContainer>
                  <StyledContainer
                    bgColor="rgb(62,63,70)"
                    style={{
                      display: 'flex',
                      alignItems: 'center',
                    }}
                    onClick={() => handleChangeGender('female')}
                  >
                    {selectedGender.female && (
                      <Fragment>
                        <IoWomanOutline color="rgb(244,213,51)" size={64} />
                        <div
                          style={{
                            display: 'flex',
                            justifyContent: 'center',
                            flex: 1,
                          }}
                        >
                          <StyledTypography
                            variant="subtitle1"
                            weight={7}
                            style={{
                              color: 'rgb(244,213,51)',
                              fontFamily: 'Lato',
                            }}
                          >
                             (Female)
                          </StyledTypography>
                        </div>
                      </Fragment>
                    )}
                    {!selectedGender.female && (
                      <Fragment>
                        <IoWomanOutline color="white" size={64} />
                        <div
                          style={{
                            display: 'flex',
                            justifyContent: 'center',
                            flex: 1,
                          }}
                        >
                          <StyledTypography
                            variant="subtitle1"
                            weight={7}
                            style={{
                              color: 'white',
                              fontFamily: 'Lato',
                            }}
                          >
                             (Female)
                          </StyledTypography>
                        </div>
                      </Fragment>
                    )}
                  </StyledContainer>
                </div>
              </Grid>
              <Grid
                item
                xs={3}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                }}
              >
                <StyledTypography variant="h6" component="h5" weight={5}>
                  
                </StyledTypography>
              </Grid>
              <Grid item xs={9}>
                <StyledInputBase name="memberName" />
              </Grid>
              <Grid
                item
                xs={3}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                }}
              >
                <StyledTypography variant="h6" component="h5" weight={5}>
                  
                </StyledTypography>
              </Grid>
              <Grid item xs={9}>
                <StyledInputBase name="memberTel" />
              </Grid>
              <Grid
                item
                xs={3}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                }}
              >
                <StyledTypography variant="h6" component="h5" weight={5}>
                  
                </StyledTypography>
              </Grid>
              <Grid item xs={9}>
                <KeyboardDatePicker
                  name="memberBirth"
                  disableFuture
                  openTo="year"
                  format="yyyy-MM-DD"
                  views={['year', 'month', 'date']}
                  value={keyboardDate}
                  onChange={handleKeyDateChange}
                  onError={handleDateError}
                  invalidDateMessage="  ."
                  maxDateMessage="    ."
                  minDateMessage="1900/01/01   ."
                />
              </Grid>
              <Grid
                item
                xs={12}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                }}
              >
                <StyledTypography variant="h6" component="h5" weight={5}>
                  
                </StyledTypography>
              </Grid>

              <Grid item xs={9}>
                <StyledInputBase
                  name="memberAddress1"
                  placeholder=" "
                  value={member_postal}
                  readOnly
                />
              </Grid>
              <Grid
                item
                xs={3}
                style={{
                  display: 'flex',
                  alignItems: 'center',
                }}
              >
                <StyledButton
                  bgColor="lightgray"
                  onClick={() => dispatch(setModalStatus(!isModalOpened))}
                  // onClick={() => setOpenModal(true)}
                >
                   
                </StyledButton>
              </Grid>
              <Grid item xs={12}>
                <StyledInputBase
                  name="memberAddress2"
                  placeholder=""
                  value={member_addr1}
                  readOnly
                />
              </Grid>
              <Grid item xs={12}>
                <StyledInputBase
                  name="memberAddress3"
                  placeholder="  ."
                />
              </Grid>
            </Grid>
            <div>
              <StyledTypography variant="h6" component="h5" weight={5}>
                
              </StyledTypography>
              <ImageUploader
                name="memberImage"
                withIcon={true}
                buttonText=" ."
                onChange={onDrop}
                imgExtension={['.jpg', '.png', '.jpeg']}
                fileSizeError="  . (5242880)"
                fileTypeError=" ."
                label="  : 5mb, : jpg, png "
                maxFileSize={5242880}
                singleImage
                withPreview
              />
            </div>
            <div>
              <StyledTypography variant="h6" component="h5" weight={5}>
                
              </StyledTypography>
              <TextField
                name="memberIntroduce"
                label="  ."
                multiline
                rows={4}
                variant="outlined"
                fullWidth
              />
            </div>
            <div
              style={{
                marginTop: 15,
                display: 'flex',
                alignItems: 'center',
              }}
            >
              <StyledTypography
                variant="h6"
                component="h5"
                weight={5}
                style={{ marginRight: 15 }}
              >
                
              </StyledTypography>
              <CirclePicker onChange={colorChange} />
              <Paper
                style={{
                  width: 60,
                  height: 60,
                  marginLeft: 30,
                  backgroundColor: currentColor,
                }}
              />
            </div>
            <div
              style={{
                position: 'fixed',
                bottom: 30,
                right: 20,
                display: 'flex',
                justifyContent: 'flex-end',
              }}
            >
              <StyledButton type="submit" bgColor="lightgray">
                 
              </StyledButton>
            </div>
            <PostalCodeModal />
          </ResponsiveContainer>
        </form>
      </SwipeableDrawer>
    </Fragment>
  );
};

export default MemberDrawer;
