import React, { useState, useRef, useEffect } from 'react';
import { getDoctorInfo, getReservationInfo } from 'apis/reservationAPI';
import { useSelector } from 'react-redux';
import {
  Divider,
  Grid,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  IconButton,
} from '@material-ui/core';
import { IoIosArrowBack, IoIosArrowForward } from 'react-icons/io';
import { RiUserSearchFill } from 'react-icons/ri';

// Toast UI Calendar Library & monent.js
import Calendar from '@toast-ui/react-calendar';
import moment from 'moment';
import 'tui-calendar/dist/tui-calendar.css';
import 'tui-date-picker/dist/tui-date-picker.css';
import 'tui-time-picker/dist/tui-time-picker.css';

// Custom Components by Team1
import ContentContainer from 'components/common/container/ContentContainer';
import TitleHeader from 'components/common/header/TitleHeader';
import ReservationDrawer from 'components/reservation/drawer/insert/ReservationDrawer';
import ReservationReadDrawer from 'components/reservation/drawer/read/ReservationReadDrawer';
import SearchReservation from 'components/reservation/drawer/searchReservation';
import ResponsivePageHeader from 'components/common/header/ResponsivePageHeader';
import PageTransition from 'components/common/transition/PageTransition';
import ClockSpinner from 'components/common/spinner/ClockSpinner';

/**
 *     ()    .
 *    .
 * *  () - Toast UI Calendar, ReservationDrawer
 * @returns {JSX.Element}
 */
const ReservationPage = () => {
  /*
   
   1) callendarDom
   2) Drawer-search
   3) Drawer-add
   4) Drawer-update
   5) selectDoctor
   6) reservationData ( , )
   7) readReservationData ( )
  */

  /*
       
  */
  const loginInfo = useSelector((state) => state.common.loginInfo);

  /*
  Calendar DOM    Ref
  : callendarDom
  */
  const calendarRef = useRef();

  /* 
  Calendar DOM  Instance   State
  : callendarDom
  */
  const [calInstance, setCalInstance] = useState(null);

  // Drawer    State

  // : Drawer-add
  const [isOpened, setOpened] = useState(false);
  // : Drawer-update
  const [readOpened, setReadOpened] = useState(false);
  // : Drawer-search
  const [searchOpened, setSearchOpened] = useState(false);

  // clockSpinner
  const [isLoading, setLoading] = useState(true);

  // errorText
  const [errorText, setError] = useState(false);

  // ,,  true  ,   addDisplay          .
  // 220 useEffect  
  const [addDisplay, setAddDisplay] = useState(false);

  /*
  Select ID   State 
  : selectDoctor
  */
  const [selectId, setSelectId] = useState('');

  /*     State (select)
  : selectDoctor
  */
  const [doctorInfo, setDoctorInfo] = useState({
    member_id: '',
    member_name: '',
    doctor_room: '',
  });
  /*
    resetvationData   State
  */
  const [reservationDataList, setReservationData] = useState([]);

  /*
        State
  */
  const [doctorListInfo, setDoctorListInfo] = useState([]);

  /*
        (onBeforeCreateSchedule)
   : reservationData
   */
  const [reservationTime, setReservationTime] = useState({
    date: '',
    startTime: '',
    endTime: '',
    scheduleStart: '',
    scheduleEnd: '',
    weekNum: '',
  });

  /* 
     updateDrawer  
  : readReservationData
  */
  const [readPatient, setReadPatient] = useState({});

  //     ,  
  const now = new Date();
  const weekNum = moment(now, 'MM-DD-YYYY').week();
  const initStartDate = moment(moment().startOf('week').toDate()).format(
    'YYYY MM DD',
  );
  const initEndDate = moment(moment().endOf('week').toDate()).format(
    'YYYY MM DD',
  );

  /*
  handlePrevClick handleNextClick  
   
    ,    
  : callendarDom
  */
  const [titleDate, setTitleDate] = useState({
    weekNo: moment(now, 'MM-DD-YYYY').week(),
    startDate: initStartDate,
    endDate: initEndDate,
  });

  // toast ui calendar dom   useEffect
  useEffect(() => {
    setCalInstance(calendarRef.current.getInstance());
  }, [calendarRef]);

  /*
       useEffect 
     ->   (setDoctorListInfo) -> select  default  (selectOnChange)
  -> reservationListData    
  */

  useEffect(() => {
    //   
    const reservationListData = async (memberId) => {
      try {
        const { data } = await getReservationInfo({
          weekNo: weekNum,
          memberId,
          hospitalCode: loginInfo.hospitalCode,
        });
        console.log(data.data);
        setReservationData(data.data);
      } catch (error) {
        console.log(error);
        setLoading(false);
      }
    };
    //   
    const doctorData = async () => {
      try {
        const { data } = await getDoctorInfo(loginInfo.hospitalCode);

        setDoctorInfo({
          memberId: data.data[0].memberId,
          memberName: data.data[0].memberName,
          doctorRoom: data.data[0].doctorRoom,
        });
        setDoctorListInfo(data.data);
        selectOnChange(data.data[0].memberId);
        reservationListData(data.data[0].memberId);
      } catch (error) {
        console.log(error);
        setLoading(false);
      }
    };

    doctorData();
  }, [loginInfo.hospitalCode, weekNum]);

  /*
    , ,  ,      useEffect 
  */
  useEffect(() => {
    const reservationListData = async () => {
      try {
        const { data } = await getReservationInfo({
          weekNo: titleDate.weekNo,
          memberId: doctorInfo.memberId,
          hospitalCode: loginInfo.hospitalCode,
        });
        setReservationData(data.data);
      } catch (error) {
        console.log(error);
        calInstance.clear();
        setReservationData([]);
        setLoading(false);
      }
    };

    if (addDisplay) {
      setLoading(true);
      setAddDisplay(false);
      reservationListData();
    }
  }, [addDisplay, titleDate, doctorInfo, loginInfo]);

  /*
     , 1    useEffect.
     calendar   .
  */
  useEffect(() => {
    if (reservationDataList.length > 0) {
      calInstance.clear();
      calInstance.createSchedules(reservationDataList);
      calInstance.render();
      setLoading(false);
    }
  }, [reservationDataList, calInstance]);

  /*
  select .
  select       
  setDoctorInfo  
  : selectDoctor
  */
  const handleMenuItemClick = async (item) => {
    setLoading(true);
    selectOnChange(item.memberId);
    const reservationInfo = async (mId) => {
      const weekNo = Number.parseInt(titleDate.weekNo);
      const memberId = Number.parseInt(mId);
      try {
        const { data } = await getReservationInfo({
          weekNo,
          memberId,
          hospitalCode: loginInfo.hospitalCode,
        });
        setReservationData(data.data);
      } catch (error) {
        console.log(error);
        setReservationData([]);
        calInstance.clear();
        setLoading(false);
      }
    };
    const data = {
      memberId: item.memberId,
      memberName: item.memberName,
      doctorRoom: item.doctorRoom,
    };
    setDoctorInfo(data);

    reservationInfo(item.memberId);
  };

  //      (-)
  const handlePrevClick = () => {
    setLoading(true);
    calInstance.prev();

    const weekNo = moment(
      calInstance.getDateRangeStart().toDate(),
      'MM-DD-YYYY',
    ).week();
    const startDate = moment(calInstance.getDateRangeStart().toDate()).format(
      'YYYY MM DD',
    );
    const endDate = moment(calInstance.getDateRangeEnd().toDate()).format(
      'YYYY MM DD',
    );

    const reservationInfo = async (mId) => {
      try {
        setTitleDate({ weekNo, startDate, endDate });
        const { data } = await getReservationInfo({
          weekNo,
          memberId: mId,
          hospitalCode: loginInfo.hospitalCode,
        });
        setReservationData(data.data);
      } catch (error) {
        console.log(error);
        setReservationData([]);
        calInstance.clear();
        setLoading(false);
      }
    };
    reservationInfo(doctorInfo.memberId);
  };

  //      (+)
  const handleNextClick = async () => {
    setLoading(true);
    calInstance.next();
    const weekNo = moment(
      calInstance.getDateRangeStart().toDate(),
      'MM-DD-YYYY',
    ).week();

    const startDate = moment(calInstance.getDateRangeStart().toDate()).format(
      'YYYY MM DD',
    );
    const endDate = moment(calInstance.getDateRangeEnd().toDate()).format(
      'YYYY MM DD',
    );

    const reservationInfo = async (mId) => {
      try {
        setTitleDate({ weekNo, startDate, endDate });
        const { data } = await getReservationInfo({
          weekNo,
          memberId: mId,
          hospitalCode: loginInfo.hospitalCode,
        });
        setReservationData(data.data);
      } catch (error) {
        console.log(error);
        setReservationData([]);
        calInstance.clear();
        setLoading(false);
      }
    };
    reservationInfo(doctorInfo.memberId);
  };

  /*
      
  1)  ,  ,    
  2)     (reservationTime) 
  3) drawer
  : readReservationData
  */
  const onBeforeCreateSchedule = (e) => {
    // 1)
    const start = e.start;
    const end = e.end;
    const date =
      moment(start).format('YYYY') +
      ' ' +
      moment(start).format('M') +
      ' ' +
      moment(start).format('D') +
      '';
    const startTime = moment(start.toDate()).format('LT');
    const endTime = moment(end.toDate()).format('LT');

    const scheduleStart = moment(start.toDate()).format();
    const scheduleEnd = moment(end.toDate()).format();

    //2)
    setReservationTime({
      date,
      start,
      end,
      startTime,
      endTime,
      scheduleStart,
      scheduleEnd,
      weekNum: titleDate.weekNo,
    });

    /*
    3)
    : Drawer-add
    */
    setOpened(true);
  };

  /*
         
  1)       id    
  2) setReadPatient   ->   PatientUpdateDrawer  
  3) PatientUpdateDrawer 
  4) : readReservationData
  */
  const onClickSchedule = (event) => {
    //1)
    const result = reservationDataList.filter(
      (schedule) => schedule.id === event.schedule.id,
    );

    /*
    2)
    : readReservationData
    */
    setReadPatient(result[0]);
    /*
    3)
    : Drawer-update
    */
    setReadOpened(true);
  };

  const selectOnChange = (value) => {
    setSelectId(value);
  };

  //      Drawer  
  const handeSearchOpenClick = () => {
    // : Drawer-search
    setSearchOpened(true);
  };

  const calendar = () => {
    return (
      <Grid container alignItems="center" justify="center">
        <Grid item xs={12}>
          <PageTransition>
            <ContentContainer>
              <TitleHeader
                style={{
                  visibility: `${!isLoading ? 'visible' : 'hidden'}`,
                }}
              >
                <div style={{ flex: 4 }}>
                  <span> | </span>
                  <span> </span>
                </div>

                {/* selectDoctor */}
                <FormControl
                  variant="standard"
                  style={{ width: '100%', flex: 1 }}
                >
                  <InputLabel id="label-id">Doctor</InputLabel>
                  <Select
                    labelId="label-id"
                    id="select-id"
                    value={selectId}
                    label="Doctor"
                  >
                    {doctorListInfo.map((item) => (
                      <MenuItem
                        key={item.memberId}
                        value={item.memberId}
                        onClick={() => handleMenuItemClick(item)}
                      >
                        {item.memberName}
                      </MenuItem>
                    ))}
                  </Select>
                </FormControl>
                {/* /selectDoctor */}
              </TitleHeader>
              <ClockSpinner
                isLoading={isLoading}
                style={{ backgroundColor: 'black' }}
              />
              <div
                className="icon-area"
                style={{
                  display: 'flex',
                  alignItems: 'center',
                  visibility: `${!isLoading ? 'visible' : 'hidden'}`,
                }}
              >
                <IconButton
                  type="button"
                  size="small"
                  style={{
                    border: '1px solid rgba(0,0,0,0.12)',
                    marginLeft: '0.5rem',
                    marginRight: '1rem',
                    padding: '0.5rem',
                  }}
                  //Drawer-search
                  onClick={handeSearchOpenClick}
                >
                  <RiUserSearchFill />
                </IconButton>
                <IconButton
                  type="button"
                  size="small"
                  style={{
                    border: '1px solid rgba(0,0,0,0.12)',
                    marginRight: '0.5rem',
                    padding: '0.5rem',
                  }}
                  onClick={handlePrevClick}
                >
                  <IoIosArrowBack />
                </IconButton>

                <IconButton
                  type="button"
                  size="small"
                  style={{
                    border: '1px solid rgba(0,0,0,0.12)',
                    marginLeft: '0.5rem',
                    marginRight: '1rem',
                    padding: '0.5rem',
                  }}
                  onClick={handleNextClick}
                >
                  <IoIosArrowForward />
                </IconButton>

                <span>
                  {titleDate.startDate} ~ {titleDate.endDate}
                </span>
              </div>

              <div
                className="calendar-area"
                style={{
                  marginTop: '1rem',
                  visibility: `${!isLoading ? 'visible' : 'hidden'}`,
                }}
              >
                <Calendar
                  height="100%"
                  view="week"
                  ref={calendarRef}
                  onBeforeCreateSchedule={onBeforeCreateSchedule}
                  onClickSchedule={onClickSchedule}
                />
              </div>
              {/* Drawer-add*/}
              <ReservationDrawer
                isOpened={isOpened}
                setOpened={setOpened}
                reservationTime={reservationTime}
                doctorInfo={doctorInfo}
                setAddDisplay={setAddDisplay}
              />
              {/* Drawer-read -> update, delete     */}
              <ReservationReadDrawer
                readOpened={readOpened}
                setReadOpened={setReadOpened}
                readPatient={readPatient}
                setAddDisplay={setAddDisplay}
              />
              {/* Drawer-search ->      drawer*/}
              <SearchReservation
                searchOpened={searchOpened}
                setSearchOpened={setSearchOpened}
                setAddDisplay={setAddDisplay}
              />
            </ContentContainer>
          </PageTransition>
        </Grid>
      </Grid>
    );
  };

  const errorPage = () => {
    return (
      <div
        style={{
          height: '100vh',
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
        }}
      >
        <h1>error!</h1>
      </div>
    );
  };

  return (
    <div>
      <header
        style={{
          position: 'sticky',
          top: 0,
          backgroundColor: 'white',
          zIndex: 1,
        }}
      >
        <ResponsivePageHeader />
        <Divider />
      </header>
      <main>
        {calendar()}
        {errorText && errorPage()}
      </main>
    </div>
  );
};

export default ReservationPage;
