import React, { Fragment, useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Grid, Hidden, List } from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import { DatePicker } from '@material-ui/pickers';
import { getNoticesList } from 'apis/noticeAPI';
import { getCoronaData } from 'apis/coronaAPI';
import { AiFillCaretUp } from 'react-icons/ai';
import {
  getTodosListByHospitalCode,
  getTodosListByMemberId,
} from 'apis/todoAPI';
import { getHospitalInfo } from 'apis/hospitalAPI';
import parse from 'html-react-parser';
import 'react-calendar/dist/Calendar.css';
import NoticeItem from 'components/dashboard/NoticeItem';
import ContentContainer from 'components/common/container/ContentContainer';
import StyledContainer from 'components/common/container/StyledContainer';
import StyledTypography from 'components/common/typography/StyledTypography';
import StyledButton from 'components/common/button/StyledButton';
import NoticeDrawer from 'components/notice/drawer/NoticeDrawer';
import ResponsivePageHeader from 'components/common/header/ResponsivePageHeader';
import PageTransition from 'components/common/transition/PageTransition';
import InputBox from 'components/todo/InputTodoBox';
import TitleHeaderDashBoard from 'pages/dashboard/front/TitleHeaderDashBoard';
import TodoItem from 'components/dashboard/TodoItem';
import moment from 'moment';
import DashBoardSpinner from 'components/common/spinner/BeatSpinner';
import { setActiveStep } from 'redux/features/notice/noticeSlice';
/**
 *         .
 *    .
 * - Sider
 * - Header
 * - 
 * - CORONA
 * - Calendar
 * - TodoItem
 * - InputBox
 * - ColoredContainer
 * @returns {JSX.Element}
 * @author HYEONG YUN KIM
 */

const FrontPage = () => {
  // NoticeDrawer Open    State
  const [isOpened, setOpened] = useState(false);
  // Data Picker    date   State
  const [date, changeDate] = useState(new Date());
  //     State
  const [todoinput, setTodoInput] = useState('');
  //     State
  const [notice, setNotice] = React.useState([]);
  //      State
  const [todo, setTodo] = React.useState([]);
  //      useEffect     State
  const [changed, setChanged] = useState(false);
  //     State
  const [hospital, setHospital] = useState('');
  //     State
  const [corona, setCorona] = useState('');
  //  ,     Weight   State
  const [mineText, setMineText] = useState(700);
  const [allText, setAllText] = useState(300);
  // Spinner Loading    State
  const [isLoading, setLoading] = useState(true);

  //     
  const dispatch = useDispatch();
  const hospitalCode = useSelector(
    (state) => state.common.loginInfo.hospitalCode,
  );
  const memberName = useSelector((state) => state.common.loginInfo.memberName);
  const memberId = useSelector((state) => state.common.loginInfo.memberId);

  // NoticeDrawer open   
  useEffect(() => {
    console.log(' .', isOpened);
  }, [isOpened]);

  // , , , TODO   
  // Spinner  . changed     .
  useEffect(() => {
    setLoading(true);
    const getNoticeAndTodoAndCorona = async () => {
      try {
        // 1.   
        const from = moment()
          .subtract(3, 'days')
          .startOf('day')
          .format('YYYY-MM-DDTHH:mm:ss');
        const to = moment().startOf('day').format('YYYY-MM-DDTHH:mm:ss');
        console.log('from :', from);

        console.log('to :', to);
        const coronaContent = await getCoronaData(from, to);
        setCorona(coronaContent);
        console.log('coronaContent : ', coronaContent);

        // 2.  
        const noticeContent = await getNoticesList(hospitalCode);
        setNotice(noticeContent.data.data);

        // 3  
        const hospitalContent = await getHospitalInfo(hospitalCode);
        setHospital(hospitalContent.data.data);
        console.log(hospitalContent.data);

        // 4  
        const todoContent = await getTodosListByMemberId(memberId);
        setTodo(todoContent.data.data);
        setChanged(false);

        // 4.   
        setLoading(false);
      } catch (error) {
        console.log(error);
        setLoading(false);
      }
    };
    getNoticeAndTodoAndCorona();
  }, [changed]);

  const TodosListByMemberId = async () => {
    try {
      const response = await getTodosListByMemberId(memberId);

      setTodo(response.data.data);

      setMineText(700);
      setAllText(300);
      setChanged(false);
    } catch (error) {
      console.log(error);
    }
  };

  const TodosListByHospitalCode = async () => {
    try {
      const response = await getTodosListByHospitalCode(hospitalCode);

      setTodo(response.data.data);
      setMineText(300);
      setAllText(700);
      setChanged(false);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    console.log(' .');
  }, []);

  const handleOpenBtn = () => {
    setOpened(true);
    dispatch(setActiveStep('MAIN'));
  };

  return (
    <Fragment>
      {isLoading && (
        <div
          style={{
            width: '100%',
            height: '90vh',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
            cursor: 'wait',
          }}
        >
          <DashBoardSpinner />
        </div>
      )}
      {!isLoading && (
        <Fragment>
          <div>
            <header
              style={{
                position: 'sticky',
                top: 0,
                backgroundColor: 'default',
                zIndex: 2,
              }}
            >
              <ResponsivePageHeader />
            </header>
            <main>
              <Grid container>
                <Grid item xs={12}>
                  <PageTransition>
                    <ContentContainer>
                      <Grid container>
                        <Grid item xs={12} sm={8} lg={8}>
                          <StyledContainer
                            bgColor="#EAF2FE"
                            padding="1.5"
                            style={{
                              display: 'flex',
                            }}
                          >
                            <div
                              style={{
                                flex: 3,
                              }}
                            >
                              <StyledTypography
                                variant="h4"
                                component="h4"
                                weight={9}
                                color="primary"
                              >
                                {hospital.hospitalName}
                              </StyledTypography>

                              <StyledTypography
                                variant="h6"
                                component="h6"
                                weight={5}
                                style={{ marginTop: '1rem' }}
                              >
                                {parse(hospital.hospitalDescription)}
                              </StyledTypography>
                            </div>
                            <Hidden xsDown>
                              <img
                                src="/assets/image/dashboard_1.png"
                                width="10%"
                                alt="Dashboard"
                                style={{
                                  flex: 1,
                                }}
                              />
                            </Hidden>
                          </StyledContainer>
                          <div style={{ marginTop: '3rem', display: 'flex' }}>
                            <div style={{ flex: 4 }}>
                              <StyledTypography
                                variant="h4"
                                component="h4"
                                weight={7}
                                style={{ marginLeft: '12px' }}
                              >
                                
                              </StyledTypography>
                            </div>
                            <div style={{ flex: 1 }}>
                              <StyledButton
                                bgColor="white"
                                onClick={handleOpenBtn}
                              >
                                <AddIcon /> 
                              </StyledButton>
                            </div>
                          </div>
                          <List component="nav">
                            {notice
                              .filter((data, index) => {
                                if (index < 4) {
                                  return true;
                                }
                                return false;
                              })
                              .map((data) => {
                                return (
                                  <NoticeItem key={data.noticeId} data={data} />
                                );
                              })}
                          </List>
                        </Grid>
                        <Grid item xs={12} sm={4} lg={4}>
                          <StyledContainer
                            bgColor="#2e51a8"
                            style={{ marginLeft: '0.7rem' }}
                          >
                            <h4
                              style={{
                                fontWeight: '300',
                                height: '100px',
                                display: 'flex',
                                alignItems: 'center',
                                justifyContent: 'center',
                                flexDirection: 'column',
                                color: '#ffffff',
                              }}
                            >
                              <TitleHeaderDashBoard>
                                <span>-19</span>
                              </TitleHeaderDashBoard>
                              <div style={{ display: 'flex' }}>
                                {
                                  corona.data[
                                    corona.data.length - 1
                                  ].Date.split('T00:00:00Z')[0]
                                }{' '}
                                
                              </div>
                            </h4>

                            <StyledContainer
                              bgColor="#ffffff"
                              style={{
                                padding: '0.75rem',
                                fontWeight: '500',
                                fontSize: '1.1rem',
                              }}
                            >
                              <div
                                style={{
                                  display: 'flex',
                                  justifyContent: 'center',
                                  alignItems: 'center',
                                  color: 'rgb(65,69,78)',
                                  padding: '10px',
                                }}
                              >
                                <div
                                  style={{
                                    flex: 1,
                                    display: 'flex',
                                    flexDirection: 'column',
                                    alignItems: 'center',
                                  }}
                                >
                                  
                                  <div>
                                    <br />
                                  </div>
                                  <div
                                    style={{
                                      color: 'rgb(236,73,64)',
                                      marginBottom: '1.5rem',
                                      display: 'flex',
                                      alignItems: 'center',
                                    }}
                                  >
                                    <AiFillCaretUp />
                                    <span
                                      style={{
                                        fontSize: '1.3rem',
                                        marginLeft: '0.3rem',
                                      }}
                                    >
                                      {corona.data[corona.data.length - 1]
                                        .Confirmed -
                                        corona.data[corona.data.length - 2]
                                          .Confirmed}
                                    </span>
                                  </div>
                                  <div>
                                    {corona.data[
                                      corona.data.length - 1
                                    ].Confirmed.toString()}
                                  </div>
                                </div>
                                <div
                                  style={{
                                    flex: 1,
                                    display: 'flex',
                                    flexDirection: 'column',
                                    alignItems: 'center',
                                  }}
                                >
                                  
                                  <div>
                                    <br />
                                  </div>
                                  <div
                                    style={{
                                      color: 'rgb(67,100,193)',
                                      marginBottom: '1.5rem',
                                      display: 'flex',
                                      alignItems: 'center',
                                    }}
                                  >
                                    <AiFillCaretUp />
                                    <span
                                      style={{
                                        fontSize: '1.3rem',
                                        marginLeft: '0.3rem',
                                      }}
                                    >
                                      {(
                                        corona.data[corona.data.length - 1]
                                          .Recovered -
                                        corona.data[corona.data.length - 2]
                                          .Recovered
                                      ).toString()}
                                    </span>
                                  </div>
                                  <div>
                                    {corona.data[
                                      corona.data.length - 1
                                    ].Recovered.toString()}
                                  </div>
                                </div>
                                <div
                                  style={{
                                    flex: 1,
                                    display: 'flex',
                                    flexDirection: 'column',
                                    alignItems: 'center',
                                  }}
                                >
                                  
                                  <div>
                                    <br />
                                  </div>
                                  <div
                                    style={{
                                      marginBottom: '1.5rem',
                                      display: 'flex',
                                      alignItems: 'center',
                                    }}
                                  >
                                    <AiFillCaretUp />
                                    <span
                                      style={{
                                        fontSize: '1.3rem',
                                        marginLeft: '0.3rem',
                                      }}
                                    >
                                      {corona.data[corona.data.length - 1]
                                        .Deaths -
                                        corona.data[corona.data.length - 2]
                                          .Deaths}
                                    </span>
                                  </div>
                                  <div>
                                    {corona.data[
                                      corona.data.length - 1
                                    ].Deaths.toString()}
                                  </div>
                                </div>
                                <div
                                  style={{
                                    flex: 1,
                                    display: 'flex',
                                    flexDirection: 'column',
                                    alignItems: 'center',
                                  }}
                                >
                                  
                                  <div>
                                    <br />
                                  </div>
                                  <div
                                    style={{
                                      marginBottom: '1.5rem',
                                      display: 'flex',
                                      alignItems: 'center',
                                    }}
                                  >
                                    <AiFillCaretUp />
                                    <span
                                      style={{
                                        fontSize: '1.3rem',
                                        marginLeft: '0.3rem',
                                        // marginBottom: '0.5rem',
                                      }}
                                    >
                                      {(
                                        corona.data[corona.data.length - 1]
                                          .Active -
                                        corona.data[corona.data.length - 2]
                                          .Active
                                      ).toString()}
                                    </span>
                                  </div>
                                  <div>
                                    {corona.data[
                                      corona.data.length - 1
                                    ].Active.toString()}
                                  </div>
                                </div>
                              </div>
                            </StyledContainer>

                            <div style={{ marginLeft: '0.3rem' }}></div>
                          </StyledContainer>

                          <Hidden xsDown>
                            <div
                              style={{
                                display: 'flex',
                                justifyContent: 'center',
                                alignItems: 'center',
                                minHeight: '400px',
                                marginTop: '1rem',
                              }}
                            >
                              <DatePicker
                                autoOk
                                clearText="CANCEL"
                                color="secondary"
                                variant="static"
                                openTo="date"
                                value={date}
                                onChange={changeDate}
                              />
                            </div>
                          </Hidden>

                          <StyledContainer
                            bgColor="rgb(203,225,253)"
                            style={{
                              marginLeft: '0.7rem',
                            }}
                          >
                            <div style={{ display: 'flex', padding: '0.5rem' }}>
                              <div style={{ flex: 3.5 }}>
                                <TitleHeaderDashBoard
                                  style={{
                                    cursor: 'pointer',
                                  }}
                                >
                                  <span
                                    style={{ fontWeight: mineText }}
                                    onClick={TodosListByMemberId}
                                  >
                                       |
                                  </span>
                                  <span
                                    style={{ fontWeight: allText }}
                                    onClick={TodosListByHospitalCode}
                                  >
                                      
                                  </span>
                                </TitleHeaderDashBoard>
                              </div>
                            </div>
                            <div
                              style={{
                                display: 'flex',
                                alignItems: 'center',
                              }}
                            >
                              <div style={{ flex: 9 }}>
                                <InputBox
                                  setTodoInput={setTodoInput}
                                  memberId={memberId}
                                  hospitalCode={hospitalCode}
                                  setChanged={setChanged}
                                  memberName={memberName}
                                  placeholder="  ."
                                />
                              </div>
                            </div>
                            <List component="nav">
                              {todo.map((data) => {
                                return (
                                  <TodoItem
                                    loginMemberId={memberId}
                                    key={data.todoId}
                                    data={data}
                                    setChanged={setChanged}
                                  />
                                );
                              })}
                            </List>
                          </StyledContainer>
                        </Grid>
                      </Grid>

                      <NoticeDrawer isOpened={isOpened} setOpened={setOpened} />
                    </ContentContainer>
                  </PageTransition>
                </Grid>
              </Grid>
            </main>
          </div>
        </Fragment>
      )}
    </Fragment>
  );
};

export default FrontPage;
